import React, { useEffect, useState, useContext } from "react";
import "./productosComponent.css";
import ProductoComponent from "../productoComponent/ProductoComponent";
import { getPlatos, getPlatosByCategoria } from "../../services/platosService";
import PosContext from "../../context/pos/posContext";

const ProductosComponent = () => {
  const { categoria_global, agregarProductoAlCarrito } = useContext(PosContext);
  const [productos, setProductos] = useState([]);
  const [cargando, setCargando] = useState(true);

  useEffect(() => {
    setCargando(true);
    if (categoria_global) {
      console.log("categoria global", categoria_global.id);
      getPlatosByCategoria(categoria_global.id)
        .then((data) => {
          console.log("TRAYENDO PLATOS POR IDCATEGORIA");
          console.log(data);
          setProductos(data);
          setCargando(false);
        })
        .catch((err) => {
          alert("ERROR AL TRAER LA INFORMACIN DE LOS PRODUCTOS DEL BACKEND");
          setCargando(false);
        });
    }
  }, [categoria_global?.id]);

  return (
    <div className="productos-component">
      <div className="productos__header p-xxl-4 pb-xxl-2 pt-xl-4 ps-xl-3 pb-xl-2 pt-3 ps-2">
        <h2 className="subtitle p-0 m-0">Productos</h2>

        <p className="p-xxl-2 p-1 m-0">{productos.length} productos</p>
      </div>

      {categoria_global !== null ? (
        !cargando ? (
          productos.length > 0 ? (
            <div className="productos-container ps-xxl-4 pe-xxl-4 ps-xl-3 pe-xl-3 p-2">
              {productos.map((prod) => {
                return (
                  <ProductoComponent
                    producto={prod}
                    key={prod.id}
                    accionName="Aadir"
                    accionFunction={agregarProductoAlCarrito}
                  />
                );
              })}
            </div>
          ) : (
            <div class="alert alert-info fs-4 m-4" role="alert">
              Sin platos en esta categora
            </div>
          )
        ) : (
          <div class="ms-4 spinner-border text-secondary" role="status">
            <span class="visually-hidden">Loading...</span>
          </div>
        )
      ) : (
        <div class="alert alert-info fs-4 m-4" role="alert">
          Selecciona una categora
        </div>
      )}
    </div>
  );
};

export default ProductosComponent;
