import React, { useContext } from "react";
import ClientesSearch from "../clientesSearch/ClientesSearch";
import PosContext from "../../context/pos/posContext";
import PerfilClienteCarrito from "../perfilClienteCarrito/PerfilClienteCarrito";
import './carritoCliente.css'
import ButtonAgregarCliente from "../buttonAgregarCliente/ButtonAgregarCliente";

const CarritoCliente = () => {

  

  const { setModalCrearCliente, cliente_global, eliminarClienteGlobal } =
    useContext(PosContext);

  const deseleccionarClienteGlobal = () => {
    eliminarClienteGlobal()
  };

  return (
    <>
      {cliente_global ? (
        <PerfilClienteCarrito
          cliente_global={cliente_global}
          deseleccionarClienteGlobal={deseleccionarClienteGlobal}
        />
      ) : (
        <div className="carrito-cliente d-flex justify-content-between align-items-center shadow p-1 rounded-pill mb-3">
          <ClientesSearch />
          
          
          <ButtonAgregarCliente setModalCrearCliente={setModalCrearCliente} />

          {/* <button
          variant="success"
            className="d-flex align-items-center justify-content-center btn-crear-cliente"
            onClick={() => setModalCrearCliente(true)}
          >
            <i class='bx bx-plus-circle bx-md'></i>
          </button> */}


        </div>
      )}
    </>
  );
};

export default CarritoCliente;
