import React, { useContext, useState } from "react";
import PedidosContext from "../../context/pedidos/pedidosContext";
import "./listaDeClientes.css";
import { MDBDataTableV5 } from "mdbreact";
import ModalEditarCliente from "../modalEditarCliente/ModalEditarCliente";
import ModalListaPedidosByCliente from "../modalListaPedidosByCliente/ModalListaPedidosByCliente";
import ModalDetallePedido from "../modalDetallePedido/ModalDetallePedido";
import ModalDetallePedidoDesdeCliente from "../modalDetallePedidoDesdeCliente/ModalDetallePedidoDesdeCliente";
import { useEffect } from "react";
import { actualizarPedido, getPedidos } from "../../services/pedidosService";

const ListaDeClientes = ({ clientes, cargando, setActualizadorClientes }) => {
  // const { pedidos, cargando: pedidosCargando } = useContext(PedidosContext);

  const [actualizarPedidos, setActualizarPedidos] = useState(false)
  const [pedidos, setPedidos] = useState([]);
  const [cargandoPedidos, setCargandoPedidos] = useState(true);
  useEffect(() => {
    setCargandoPedidos(true);
    getPedidos().then(data => {
      // console.log("PEDIDOS: ", data);
      setPedidos(data);
      setCargandoPedidos(false) 
    })
  }, [actualizarPedidos])

  // VARIABLES QUE USA EL MODAL EDITAR CLIENTE
  const [idClienteEditar, setIdClienteEditar] = useState(null);
  const [activarModalEditarCliente, setActivarModalEditarCliente] =
    useState(false);
  const [activarModalVerPedidos, setActivarModalVerPedidos] = useState(false)
  const [idClientePedidos, setIdClientePedidos] = useState(null)
  // ========================================

  // VARIABLES QUE USA EL MODAL DETALLE PEDIDO
  const [activarModalDetallePedido, setActivarModalDetallePedido] = useState(false)
  const [idPedidoDetalle, setIdPedidoDetalle] = useState(null)
  // =========================================

  const pedidosXCliente = (idCliente, pedidos) => {
    let pedidosCliente = pedidos?.filter(pedido =>  pedido.cliente.id === idCliente)
    return pedidosCliente;
  }

  const deudaPorCliente = (idCliente, pedidos) => {
    let pedidosCliente = pedidosXCliente(idCliente, pedidos);
    // Calculamos la deuda
    let deuda = pedidosCliente?.reduce((contador, pedido) => {
      return (contador += (pedido.monto_a_pagar - pedido.monto_pagado));
    }, 0);
    // console.log("DEUDA: ", deuda);
    return deuda;
  };

  let filas = [];

  // Seteamos los datos iniciales de la tabla
  let dataTable = {
    columns: [
      { label: "Nombre", field: "cliente" },
      { label: "Celular", field: "celular" },
      { label: "Pedidos realizados", field: "numero_pedidos" },
      { label: "Pago", field: "pago" },
      { label: "Editar", field: "editar" },
      { label: "Pedidos", field: "pedidos" },
    ],

    rows: [
      {
        cliente: 1,
        celular: 2,
        numero_pedidos: 3,
        pago: 4,
        editar: 5,
        pedidos: 6,
      },
    ],
  };

  // Cuando los clientes terminen de cargar, armamos las filas usando los datos
  if (cargando === false && cargandoPedidos === false) {

    filas = clientes.map((cliente) => {
      let deudaCliente = deudaPorCliente(cliente.id, pedidos);
      return {
        cliente: cliente.nombre + " " + cliente.apellido,
        celular: cliente.celular.length > 0? cliente.celular : " --------- ",
        numero_pedidos: pedidosXCliente(cliente.id, pedidos).length,
        pago: deudaCliente > 0 ? `Debe S/ ${deudaCliente}`: "Completo",
        editar: (
          <button
            className="btn btn-table-secondary"
            onClick={() => {
              setIdClienteEditar(cliente.id)
              setActivarModalEditarCliente(true);
            }}
          >
            Editar
          </button>
        ),
        pedidos: pedidosXCliente(cliente.id, pedidos).length > 0?
        <button
        onClick={() => {
          setIdClientePedidos(cliente.id)
          setActivarModalVerPedidos(true)
        }}
        className="btn btn-table-brown"
        >Pedidos</button>
        : 
        <button
        
        className="btn btn-light fs-6"
        >Sin pedidos</button>
        

      };
    });

    // Luego le pasamos las nuevas filas a los datos de la tabla
    dataTable = {
      ...dataTable,
      rows: filas,
    };
  }

  return (
    <div>
      {/* MODAL EDITAR CLIENTE */}
      {activarModalEditarCliente && (
        <ModalEditarCliente
          activarModalEditarCliente={activarModalEditarCliente}
          setActivarModalEditarCliente={setActivarModalEditarCliente}
          idCliente={idClienteEditar}
          setActualizadorClientes={setActualizadorClientes}
        />
      )}
      {/* MODAL PARA VER PEDIDOS DE CADA CLIENTE */}
      {activarModalVerPedidos  && 
      <ModalListaPedidosByCliente
      setIdPedidoDetalle={setIdPedidoDetalle}
      setActivarModalDetallePedido={setActivarModalDetallePedido}
      idCliente={idClientePedidos}
      activarModalVerPedidos={activarModalVerPedidos}
      // ESTE MODAL TIENE MODAL TIENE LA OPCION DE ABRIR EL MODAL DETALLE DE UN PEDIDO
      setActivarModalVerPedidos={setActivarModalVerPedidos}
      /> 
      
    }
    {/* MODAL PARA VER EL DETALLE DE UN PEDIDO  */}
    {
      activarModalDetallePedido && 
      <ModalDetallePedidoDesdeCliente
      setActualizarPedidos={setActualizarPedidos}
      activarModalDetallePedido={activarModalDetallePedido}
      setActivarModalDetallePedido={setActivarModalDetallePedido}
      idPedidoDetalle={idPedidoDetalle}
      setActualizarContenedor={setActualizadorClientes}
      // ABRIR EL MODAL LISTA DE PEDIDOS, CUANDO ESTE MODAL SE CIERRE
      setActivarModalVerPedidos={setActivarModalVerPedidos}
      setActualizadorClientes={setActualizadorClientes}
      />

    }


      <div className="lista-clientes mt-xxl-5 pt-xxl-5 mb-xxl-5 mt-4 mb-4 pt-xl-4 p-4 shadow">
        <div className="lista-clientes__header">
          <h2 className="subtitle">Lista de clientes</h2>
          {/* <p className="p-2">Filtrar clientes</p> */}
        </div>

        <div className="lista-clientes__container p-4 p-xl-0">
          {cargando ? (
            <div className="text-center">
              <div class="spinner-border text-secondary" role="status">
                <span class="visually-hidden">Loading...</span>
              </div>
            </div>
          ) : (
            <MDBDataTableV5 data={dataTable} />
          )}
        </div>
      </div>
    </div>
  );
};

export default ListaDeClientes;
