import React from "react";
import "./mostOrderedItem.css";
import imgProduct from "../../assets/images/frapuchino.png";

const MostOrderedItem = ({ plato }) => {
  return (
    <li className="most-ordered-item">
      <div className="most-ordered__img">
        <img src={plato.imagen} alt="" />
      </div>
      <div>
        <p className="most-ordered__name">{plato.nombre}</p>
        <p className="most-ordered__quantify">{`${plato.cantidad_vendida} unidades vendidas`}</p>
      </div>
    </li>
  );
};

export default MostOrderedItem;
