import React, { useState } from 'react'
import './platoAdministrar.css'
import productImg from '../../assets/images/frapuchino.png'
import ModalEditarProducto from '../modalEditarProducto/ModalEditarProducto'

const PlatoAdministrar = ({plato, categorias}) => {

    const [modalEditarCliente, setModalEditarCliente] = useState(false)
    
    return (
        <>
        <ModalEditarProducto categorias={categorias} plato={plato} modalEditarCliente={modalEditarCliente} setModalEditarCliente={setModalEditarCliente} />
        <div className="plato-component shadow">
            <div className="plato-img">
                <img src={plato.imagen} alt="" />
            </div>
            <div className="plato-body">
                <h3 className="plato-title">
                    {plato.nombre}
                </h3>
                <p className="plato-price">
                    S/ {plato.precio}
                </p>
                {/* <div className="plato-size">
                    <p>Tamao</p>
                    <div className="size-options">
                        <p>S</p>
                        <p>M</p>
                        <p>L</p>
                    </div>
                </div> */}
                <p className="plato-stock">
                    {plato.stock} disponibles
                </p>
            </div>
            <div className="plato-footer">
                <button 
                onClick={() => {setModalEditarCliente(true)}}
                className="agregarplato-btn">Editar</button>
            </div>
        </div>
        </>
    )
}

export default PlatoAdministrar
