import React, { useState } from "react";
import "./reportePedidos.css";
import ModalDetallePedido from "../modalDetallePedido/ModalDetallePedido";
import { MDBDataTableV5 } from 'mdbreact';
import moment from 'moment'

const ReportePedidosComponent = ({ pedidos, cargando, setActualizarPedidos }) => {
  const [activarModalDetallePedido, setActivarModalDetallePedido] =
    useState(false);
  const [idPedidoDetalle, setIdPedidoDetalle] = useState(null);

  // let idPedidoDetalle = null;

  const cantidadPlatos = (pedido) => {
    let cantidad = pedido.platos.reduce((contador, plato) => {
      return (contador += plato.cantidad);
    }, 0);
    return cantidad;
  };

  let filas = [];

  // Seteamos los datos de la tabla con las filas vacias
  let dataTable = {
    columns: [
      { label: "Nombre", field: "cliente" },
      { label: "Fecha", field: "fecha" },
      { label: "Cantidad Platos", field: "cantidad_platos" },
      { label: "Total", field: "total" },
      { label: "Entrega", field: "entrega" },
      { label: "Estado", field: "estado" },
      { label: "Acciones", field: "acciones" },
    ],
    rows: [
      {
        cliente: 1,
        fecha: 2,
        cantidad_platos: 3,
        total: 4,
        entrega: 5,
        estado: 6,
        acciones: <>Hola</>,
      },
    ],
  };

  // Cuando los pedidos terminen de cargar, armamos las filas que irn en la tabla usando los pedidos
  if (cargando === false) {
    // pedidos.sort((a, b) => moment(a.fecha).isAfter(b.fecha)? - 1: 1)
    let pedidosOrdenados = pedidos.sort((a, b) => moment(a.fecha).isAfter(b.fecha) ?  -1 :  1);
    console.log("Ordenado: ", pedidosOrdenados);

    filas = pedidosOrdenados.map((pedido) => ({
      cliente: `${pedido.cliente?.nombre} ${pedido.cliente?.apellido}`,
      fecha: pedido.fecha,
      cantidad_platos: cantidadPlatos(pedido),
      total: "S/ " + pedido.monto_a_pagar,
      entrega:pedido.estado === "entregado" ? 
         <span className={"badge-pedido entregado"}>Entregado</span>
      : 
      <span className={"badge-pedido pendiente"}>Pendiente</span>
        
      
  
      ,
      estado: pedido.monto_pagado === pedido.monto_a_pagar ? 
             <span className={"badge-pedido pagado"}>Pagado</span> 
             : 
              <span className={"badge-pedido debe"}>
                {/* Debe S/ {`${pedido.monto_a_pagar - pedido.monto_pagado}`} */}
                Debe
              </span>
              
            
      
      ,
      acciones: 
      (
        <button
          onClick={ () => {
            setIdPedidoDetalle(pedido.id)
            // idPedidoDetalle = pedido.id
            setActivarModalDetallePedido(true)
          }}
          className="btn-ver-detalle"
        >
          Ver pedido
        </button>
      ),
    }));
    // Luego le pasamos las nuevas filas a los datos de la tabla
    dataTable = {
      ...dataTable,
      rows: filas,
    };
  }

  console.log(idPedidoDetalle, "Pedido detalle reporte Pedidos");

  return (
    <div className="reporte-pedidos mt-xxl-5 pt-xxl-5 mb-xxl-5 mt-4 mb-4 pt-xl-4 p-4 shadow">
      <div className="reporte-pedidos__header">
        <h2 className="subtitle">Reporte de pedidos</h2>

        {/* <p className="p-2 p-xl-0">Filtrar Orden</p> */}
      </div>

      <div className="reporte-pedidos__container p-4 p-xl-0">
        {/* MODAL DE DETALLE DEL PEDIDO */}
        {
          activarModalDetallePedido ?
          <ModalDetallePedido
            activarModalDetallePedido={activarModalDetallePedido}
            setActivarModalDetallePedido={setActivarModalDetallePedido}
            idPedidoDetalle={idPedidoDetalle}
            setActualizarPedidos={setActualizarPedidos}
          />
          : 
          null
        }
        {/* ************************* */}
        {cargando ? (
          <div className="text-center">
            <div class="spinner-border text-secondary" role="status">
              <span class="visually-hidden">Loading...</span>
            </div>
          </div>
        ) : (
          <div className="table">

            <MDBDataTableV5 data={dataTable} hover maxHeight='500px'/>
          </div>
        )}
      </div>
    </div>
  );
};

export default ReportePedidosComponent;
