import React, { useState, useEffect } from "react";
import { Modal } from "react-bootstrap";
import { actualizarPedido, getPedidoById } from "../../services/pedidosService";
import { MDBDataTableV5 } from "mdbreact";
import Swal from "sweetalert2";
import DeliveryStatus from "../deliveryStatus/DeliveryStatus";
import './modalDetallePedido.css'

const ModalDetallePedido = ({
  activarModalDetallePedido,
  setActivarModalDetallePedido,
  idPedidoDetalle,
  setActualizarPedidos,
}) => {
  const [pedido, setPedido] = useState(null);
  const [cargando, setCargando] = useState(true);
  const [cargandoActualizarPedido, setCargandoActualizarPedido] = useState(false);
  const [mensajeError, setMensajeError] = useState(null);
  const [actualizarEstePedido, setActualizarEstePedido] = useState(false);

  console.log("CARGA INICIAL DETALLE PEDIDO", cargando);
  console.log(pedido);

  useEffect(() => {
    if (!idPedidoDetalle) return;
    getPedidoById(idPedidoDetalle)
      .then((data) => {
        setPedido(data);
        setCargando(false);
        console.log("CARGA 2", cargando);
      })
      .catch((error) => {
        setMensajeError(
          "Error al ver el detalle del pedido, vuelva a intentar"
        );
        setCargando(false);
      });
  }, [idPedidoDetalle, actualizarEstePedido]);

  let filas = [];

  let dataTable = {
    columns: [
      { label: "Plato", field: "nombre" },
      { label: "Categoria", field: "categoria" },
      { label: "Cantidad", field: "cantidad_platos" },
      { label: "Precio", field: "precio" },
      { label: "Total", field: "total" },
    ],
    rows: [
      {
        nombre: 1,
        categoria: 2,
        cantidad_platos: 3,
        precio: 4,
        total: 5,
      },
    ],
  };

  if (!cargando) {
    filas = pedido?.platos?.map((plato) => ({
      nombre: plato?.plato_nombre,
      categoria: plato?.categoria.nombre,
      cantidad_platos: plato?.cantidad,
      precio: `S/ ${plato?.plato_precio}`,
      total: `S/ ${plato?.cantidad * plato?.plato_precio}`,
    }));

    // Luego le pasamos las nuevas filas a los datos de la tabla
    dataTable = {
      ...dataTable,
      rows: filas,
    };
  }

  const pagarPedido = (idPedido) => {
    Swal.fire({
      title: "Desea confirmar el pago de este pedido",
      icon: "question",
      showCancelButton: true,
    }).then((rpta) => {
      if (rpta.isConfirmed) {
        setCargandoActualizarPedido(true);
        actualizarPedido(idPedido, { monto_pagado: pedido.monto_a_pagar }).then(
          (rpta) => {
            if (rpta?.length > 0) {
              setActualizarPedidos((prevState) => !prevState);
              Swal.fire({
                title: "Pedido pagado",
                icon: "success",
                showCancelButton: false,
                timer: 800,
              });
              setCargandoActualizarPedido(false);
              setActualizarEstePedido((prevState) => !prevState);
            }
          }
        );
      }
    });
  };

  return (
    <Modal
      size="lg"
      show={activarModalDetallePedido}
      onHide={() => {
        setActivarModalDetallePedido(false);
      }}
      keyboard={false}
      centered
    >
      <Modal.Header>
        <Modal.Title>Detalle del pedido</Modal.Title>
        <h5 className="me-5">{pedido?.fecha}</h5>
      </Modal.Header>

      <Modal.Body>
        <div style={{ position: "relative" }}>
          {cargandoActualizarPedido ? (
            <div className="spinner-wrapper d-flex justify-content-center align-items-center">
              
              <div className="text-center">
                <div className="spinner-border text-secondary" role="status">
                  <span className="visually-hidden">Loading...</span>
                </div>
              </div>
            </div>
          ) : cargando ? null : (
            <div>
              {/* STATUS DE LA ENTREGA */}
              <DeliveryStatus
              setCargandoActualizarPedido={setCargandoActualizarPedido}
                idPedido={idPedidoDetalle}
                estado={pedido?.estado}
                setActualizarEstePedido={setActualizarEstePedido}
                setActualizarPedidos={setActualizarPedidos}
              />
              {/* INFORMACION DEL CLIENTE */}
              <div className="row ps-2 ms-2 me-5">
                <div className="col-8">
                  <div className="d-flex gap-2">
                    Cliente:{" "}
                    <p>
                    <b>{`${pedido?.cliente.nombre} ${pedido?.cliente.apellido}`}</b>

                    </p>
                  </div>
                  <p>
                    Celular:{" "}
                    {pedido?.cliente?.celular?.length > 0 ? (
                      <b>{pedido?.cliente?.celular}</b>
                    ) : (
                      " ---------"
                    )}
                  </p>
                </div>
                <div className="col-4 text-end">
                  {pedido?.monto_pagado === pedido?.monto_a_pagar ? (
                    <span className="badge-pedido pagado fs-5">Pagado</span>
                  ) : (
                    <p>
                      <button
                        style={{ outline: "none", border: "none" }}
                        className="badge-pedido debe fs-5 p-2 ps-3 pe-3 pulse-button-pagar"
                        onClick={() => pagarPedido(idPedidoDetalle)}
                      >
                        Pagar S/ {pedido?.monto_a_pagar - pedido?.monto_pagado}
                      </button>
                    </p>
                  )}
                </div>
              </div>
            </div>
          )}
          <hr className="m-4 me-5" />
          <div className="platos m-4 me-5">
            <p className="fs-5" style={{ "fontWeight": "500" }}>
              Platos
            </p>
            {mensajeError ? (
              <div class="alert alert-warning" role="alert">
                {mensajeError}
              </div>
            ) : cargando ? (
              <div className="text-center">
                <div className="spinner-border text-secondary" role="status">
                  <span className="visually-hidden">Loading...</span>
                </div>
              </div>
            ) : (
              <MDBDataTableV5
                searching={false}
                sortable={false}
                info={false}
                paging={false}
                data={dataTable}
              />
            )}
            <hr />
            <div className="col-12 text-end pe-2">
              <p className="mb-0" style={{ "font-weight": "500" }}>
                Total
              </p>
              <p className="fs-3" style={{ "font-weight": "500" }}>
                S/ {pedido?.monto_a_pagar}
              </p>
            </div>
          </div>
        </div>
      </Modal.Body>

      <Modal.Footer>
        <button
          className="btn btn-dark"
          onClick={() => {
            setPedido(null);
            setCargando(true);
            setActivarModalDetallePedido(false);
            setCargandoActualizarPedido(false);
            // setMensajeError(null);
          }}
        >
          Cerrar
        </button>
      </Modal.Footer>
    </Modal>
  );
};

export default ModalDetallePedido;
