import React, { useContext, useState } from "react";
import "./itemCarrito.css";
import itemPhoto from "../../assets/images/frapuchino.png";
import PosContext from "../../context/pos/posContext";

const ItemCarrito = ({plato}) => {

  const {eliminarProductoDelCarrito, editarCantidadDelProducto,actualizarMontoTotal } = useContext(PosContext);

  const handleChangeCantidad = (e) => {
    editarCantidadDelProducto(plato.plato_id, +e.target.value);
    actualizarMontoTotal();
  }


  return (
    <div className="item-carrito">
      <div className="item-img">
        <img src={plato.imagen} alt="" />
      </div>
      <div className="item-info">
        <div className="item-info__header">
        <p className="item-title">{plato.plato_nombre}</p>
        <button 
        onClick={() => {
          eliminarProductoDelCarrito(plato.plato_id)
        
        } }
        className="btn-delete">
        <i class='bx bx-x bx-sm' ></i>
        </button>
        </div>

        <div className="item-details">
          <div>
            <p>
              x
            </p>
            {/* CANTIDAD PLATO */}
            <input 
            min="1"
            onChange={handleChangeCantidad}
            className="item-quantify" type="number" value={plato.cantidad}/>
            {/* <button className="item-notes">Notes</button> */}
          </div>
          {/* CANTIDAD PRECIO */}
          <p className="item-price">S/{plato.plato_precio}</p>
        </div>
      </div>
    </div>
  );
};

export default ItemCarrito;
