import React, { useContext, useState } from "react";
import { NavLink } from "react-router-dom";
import "boxicons";
import "./menuComponent.css";
import AuthContext from "../../context/auth/authContext";

const MenuComponent = () => {

  const {cerrarSesion} = useContext(AuthContext)


  return (
    <nav className="menuComponent shadow-sm">
      <div className="nav__logo">
        <i className="bx bx-md bxs-store"></i>
      </div>
      <ul className="nav__menu ">
        <li>
          <NavLink
            activeClassName="menuComponent-active"
            className="menu__link"
            to={"/vender"}
          >
            <i className="bx-md bx bx-dish"></i>
            Vender
          </NavLink>
        </li>

        <li>
          <NavLink
            activeClassName="menuComponent-active"
            className="menu__link"
            to={"/pedidos"}
          >
            <i className="bx-md bx bx-time-five"></i>
            Pedidos
          </NavLink>
        </li>

        {/* <li>
          <NavLink
          // 
          activeClassName="menuComponent-active" className="menu__link" to={"/pagos"}>
          <i className='bx-md bx bx-wallet' ></i>
                    Pagos
          </NavLink>
        </li> */}

        <li>
          <NavLink
            activeClassName="menuComponent-active"
            className="menu__link"
            to={"/clientes"}
          >
            <i className="bx-md bx bx-group"></i>
            Clientes
          </NavLink>
        </li>

        <li>
          <NavLink
            activeClassName="menuComponent-active"
            className="menu__link menu--administrar"
            to={"/administrar"}
          >
            <i className="bx-md bx bx-wrench"></i>
            Administrar
          </NavLink>
        </li>
      </ul>
      <div className="btn-logout__wrapper">
      <button className="btn-logout" 
      onClick={cerrarSesion}
      >
      <i class='bx bx-log-out bx-md'></i>
      Salir
      </button>

      </div>
    </nav>
  );
};

export default MenuComponent;
