import React from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import AdministrarProductos from "../components/administrarProductos/AdministrarProductos";
import AdministrarSeguridad from "../components/administrarSeguridad/AdministrarSeguridad";
import SettingsMenu from "../components/settingsMenu/SettingsMenu";
import AdministrarProductosState from "../context/AdministrarProductos/administrarProductosState";

const AdministrarScreen = () => {
  return (
    <AdministrarProductosState>
      <section className="row p-xxl-4 pb-xxl-2 pt-xl-4 ps-xl-4 pb-xl-2 p-2 pt-3 m-0">
        <h2 className="title">Administrar</h2>

        <div className="col-3 pt-xxl-4 pt-xl-3 pt-2">
          <SettingsMenu />
        </div>

        <div className="col-9 pt-xxl-4 pt-xl-3 pt-2">
          <Switch>
            <Route
              path="/administrar/productos"
              component={AdministrarProductos}
            />
            <Route
              path="/administrar/seguridad"
              component={AdministrarSeguridad}
            />
          </Switch>
          <Redirect to="/administrar/productos" />
        </div>
      </section>
    </AdministrarProductosState>
  );
};

export default AdministrarScreen;
