import React, { useState, useContext } from "react";
import PosContext from "../../context/pos/posContext";
import ItemCarrito from "../itemCarrito/ItemCarrito";
import "./confirmarOrdenComponent.css";
import Swal from 'sweetalert2';

const ConfirmarOrdenComponent = ({ setConfirmarPedido }) => {

  const { carrito, monto_total, monto_pagado, establecerEstadoDelPedido, establecerMontoPagado, estado, crearPedido } =
    useContext(PosContext);
  const [montoACuenta, setMontoACuenta] = useState(0);

  const handleChange = (e) => {
    switch (e.target.name) {
      case "estado":
        establecerEstadoDelPedido(e.target.value)
        
        break;
      case "monto_pagado":
        if (e.target.value < 0 || e.target.value > monto_total) return;
        setMontoACuenta(e.target.value);
        establecerMontoPagado(e.target.value)
        break;
      default:
        break;
    }
  };

  

  return (
    <div className="confirmarOrden p-xxl-5 p-xl-4 pt-xl-3 p-3 shadow-sm">
      <div className="confirmarOrden-header mb-0 p-0">
        <h2 className="confirmarOrden-title">Confirmar rden</h2>
      </div>

      <div className="confirmarOrden-body ps-0 pe-2">
        <ul className="p-0 ms-0">
          {carrito.map((plato) => {
            return (
              <li >
                <ItemCarrito plato={plato} />
              </li>
            );
          })}
        </ul>
      </div>

      <div className="confirmarOrden-footer pb-xxl-5 pb-xl-2 ps-xxl-5 ps-xl-4 pe-xxl-5 pe-xl-4 p-3 shadow">
        <div className="confirmarOrden-status pt-xxl-2 pt-xl-1">
          <p className="confirmarOrden-subtitle mb-xxl-3 mb-xl-2">Estado de Pedido</p>
          <div>
            <button
              onClick={handleChange}
              name="estado"
              value="pendiente"
              className={estado === "pendiente" && "active-state"}
            >
              Pendiente
            </button>
            <button
              onClick={handleChange}
              name="estado"
              value="entregado"
              className={estado !== "pendiente" && "active-state"}
            >
              Entregado
            </button>
          </div>
        </div>

        <div className="confirmarOrden-pago pt-0 mb-3">
          <div className="confirmarOrden-precio">
            <p className="confirmarOrden-subtitle mb-0 ">Total</p>
            <p className="total-price mb-0  confirmarOrden-subtitle">
              S/ {monto_total}
            </p>
          </div>

          <p className="confirmarOrden-subtitle text-center mb-xxl-3 mb-xl-2">Pagar</p>
          <div className="confirmarOrden-pagar">
            <button
              onClick={handleChange}
              name="monto_pagado"
              value={monto_total}
              className={
                monto_pagado === monto_total
                  ? "btn-pagar btn-pagar-active"
                  : "btn-pagar"
              }
            >
              <i class="bx bx-md bx-money"></i>
              Completo
            </button>

            <input
              onChange={handleChange}
              name="monto_pagado"
              className={
                montoACuenta > 0
                  ? "btn-pagar btn-pagar-active text-center inputACuenta"
                  : "btn-pagar text-center inputACuenta"
              }
              type="number"
              placeholder="S/"
              value={montoACuenta}
            />
          </div>
        </div>

        <div className="confirmarOrden-btns-container">
          <button
            onClick={() => setConfirmarPedido(false)}
            className="confirmarOrden-btn"
          >
            Cancelar
          </button>

          <button 
          onClick={crearPedido}
          className="confirmarOrden-btn">Confirmar</button>
        </div>
      </div>
    </div>
  );
};

export default ConfirmarOrdenComponent;
