import React, { useState } from "react";
import { postPedidos } from "../../services/pedidosService";
import PosContext from "./posContext";
import Swal from "sweetalert2";
import moment from "moment";

const PosState = ({ children }) => {
  moment.locale("es");

  console.log("ACTUALIZANDO POS STATE");

  let posStateVacio = {
    categoria_global: null,
    carrito: [],
    estado: "pendiente",
    cliente_global: null,
    monto_total: 0,
    monto_pagado: 0,
  };

  const [puntoDeVentaState, setPuntoDeVentaState] = useState(posStateVacio);

  const [modalCrearCliente, setModalCrearCliente] = useState(false);

  const establecerCategoriaGlobal = (objCategoria) => {
    setPuntoDeVentaState((prevState) => ({
      ...prevState,
      categoria_global: objCategoria,
    }));
  };

  const establecerClienteGlobal = (objCliente) => {
    setPuntoDeVentaState((prevState) => ({
      ...prevState,
      cliente_global: objCliente,
    }));
  };

  const eliminarClienteGlobal = () => {
    setPuntoDeVentaState((prevState) => ({
      ...prevState,
      cliente_global: null,
    }));
  }

  const agregarProductoAlCarrito = (objProduct) => {
    // Sacamos una copia del carrito del puntoDeVentaState
    const { carrito } = puntoDeVentaState;

    // Si el carrito esta vacio
    // Si el carrito ya tiene un producto diferente
    // Si el carrito ya tiene el mismo producto

    // Verificar si el carrito ya tiene el plato que queremos agregar
    let platoRepetido = carrito.find(
      (plato) => plato.plato_id === objProduct.id
    );

    if (platoRepetido) {
      // la variable platoRepetido es un puntero al plato repetido en el arreglo carrito
      // por lo que al modificar esta variable se modificara el plato en el carrito
      platoRepetido.cantidad++;

      // Calculamos el monto total
      let montoTotal = carrito.reduce((contador, plato) => {
        return (contador += plato.plato_precio * plato.cantidad);
      }, 0);

      // Actualizamos el PuntoDeVentaState
      setPuntoDeVentaState((prevState) => ({
        ...prevState,
        carrito: carrito,
        monto_total: montoTotal,
      }));
    } else {
      // Si el producto no esta en el carrito, lo agregamos
      carrito.push({
        plato_nombre: objProduct.nombre,
        plato_id: objProduct.id,
        categoria: objProduct.categoria,
        cantidad: 1,
        plato_precio: objProduct.precio,
        imagen: objProduct.imagen,
      });
      // Calculamos el monto total
      let montoTotal = carrito.reduce((contador, plato) => {
        return (contador += plato.plato_precio * plato.cantidad);
      }, 0);

      // Seteamos el state con el arreglo carrito
      setPuntoDeVentaState((prevState) => ({
        ...prevState,
        carrito: carrito,
        monto_total: montoTotal,
      }));
    }
  };

  const editarCantidadDelProducto = (idProducto, cantidad) => {

    let { carrito } = puntoDeVentaState;

    let platoActualizar = carrito.find(
      (plato) => plato.plato_id === idProducto
    );

    platoActualizar.cantidad = cantidad;

    setPuntoDeVentaState((prevState) => ({
      ...prevState,
      carrito: carrito,
    }));
  };

  const eliminarProductoDelCarrito = (idProducto) => {
    let { carrito } = puntoDeVentaState;

    carrito = carrito.filter((item) => item.plato_id !== idProducto);

    let montoTotal =  carrito.reduce((contador, plato) => {
      return contador += plato.plato_precio * plato.cantidad;
    }, 0)

    setPuntoDeVentaState((prevState) => ({
      ...prevState,
      carrito: carrito,
      monto_total: montoTotal,
    }));


  };

  const establecerEstadoDelPedido = (estado) => {
    setPuntoDeVentaState((prevState) => ({
      ...prevState,
      estado: estado,
    }));
  };

  const establecerMontoPagado = (monto) => {
    setPuntoDeVentaState((prevState) => ({
      ...prevState,
      monto_pagado: +monto,
    }));
  };

  const crearPedido = () => {
    let pedido = {
      cliente: puntoDeVentaState.cliente_global,
      platos: puntoDeVentaState.carrito,
      estado: puntoDeVentaState.estado,
      monto_pagado: puntoDeVentaState.monto_pagado,
      monto_a_pagar: puntoDeVentaState.monto_total,
      fecha: moment().format("DD/MM/YYYY"),
    };

    Swal.fire({
      title: "Desea crear el pedido?",
      icon: "question",
      showCancelButton: true,
    }).then((rpta) => {
      if (rpta.isConfirmed) {
        postPedidos(pedido).then((rpta) => {
          if (rpta.id) {
            setPuntoDeVentaState({
              ...posStateVacio,
              categoria_global: puntoDeVentaState.categoria_global,
            });
            Swal.fire({
              title: "Pedido creado con xito",
              icon: "success",
              timer: 1000,
            });
          }
        });
      }
    });
  };

  const actualizarMontoTotal = () => {
    console.log("==============================");
    console.log("ACTUALIZANDO MONTO");
    let montoActualizado = puntoDeVentaState.carrito.reduce(
      (contador, plato) => {
        return (contador += plato.plato_precio * plato.cantidad);
      },
      0
    );
    console.log("MONTO ACTUALIZADO : ", montoActualizado);
    setPuntoDeVentaState((prevState) => ({
      ...prevState,
      monto_total: montoActualizado,
    }));
  };

  return (
    <PosContext.Provider
      value={{
        puntoDeVentaState,
        establecerCategoriaGlobal,
        establecerClienteGlobal,
        eliminarClienteGlobal,
        establecerEstadoDelPedido,
        establecerMontoPagado,
        // setPuntoDeVentaState,
        agregarProductoAlCarrito,
        carrito: puntoDeVentaState.carrito,
        monto_total: puntoDeVentaState.monto_total,
        monto_pagado: puntoDeVentaState.monto_pagado,
        cliente_global: puntoDeVentaState.cliente_global,
        modalCrearCliente,
        setModalCrearCliente,
        estado: puntoDeVentaState.estado,
        crearPedido,
        eliminarProductoDelCarrito,
        editarCantidadDelProducto,
        categoria_global: puntoDeVentaState.categoria_global,
        actualizarMontoTotal,
      }}
    >
      {children}
    </PosContext.Provider>
  );
};

export default PosState;
