import React from "react";
import "./deliveryStatus.css";
import Swal from 'sweetalert2';
import { actualizarPedido } from "../../services/pedidosService";

const DeliveryStatus = ({idPedido, estado, setActualizarEstePedido, setActualizarPedidos, setCargandoActualizarPedido}) => {

  

 console.log("estado: ", estado);

  const actualizarEstadoDelivery = () => {
      console.log("ACTUALIZAR PEDIDO");
    Swal.fire({
        title: "Desea actualizar el estado de la entrega",
        icon: "question",
        showCancelButton: true,
    }).then(rpta => {
        if (rpta.isConfirmed){
            setCargandoActualizarPedido(true);
            actualizarPedido( idPedido, { estado: "entregado"}).then(rpta => {
                if (rpta?.length > 0) {
                    setCargandoActualizarPedido(false)
                    setActualizarEstePedido(prevState =>  !prevState)
                    setActualizarPedidos(prevState => !prevState)
                    Swal.fire({
                        title: "Estado del pedido actualizado",
                        icon: "success",
                        showCancelButton: false,
                        timer: 800
                    })
                } else {
                    Swal.fire({
                        title: "Oops...",
                        text: "Error al editar el estado del pedido, intentelo ms tarde",
                        icon: "error",
                        showCancelButton: true,
                    })
                }
            })
        }
    })
  }




  return (
      <div className="mb-4">

      <h4 className="text-center mb-4">Estado de la entrega</h4>
    <div className="d-flex justify-content-center">
      
      <div className="item-status"
      >
        <p className="item-status-icon status-icon--active mb-1">
          <i class="bx bx-sm bxs-basket"></i>
        </p>
        <p className="status-caption status-caption--active">Orden <br/> Generada</p>
      </div>



      <div className="regla-status regla-status--active"></div>

      <div className="item-status" 
      >
        <p className="item-status-icon status-icon--active mb-1">
          <i class="bx bx-sm bxs-cart"></i>
        </p>

        <p className="status-caption status-caption--active">Pendiente</p>
      </div>


      <div className={estado === "entregado"? "regla-status regla-status--active": "regla-status"}></div>

      <div className="item-status"
      onClick={estado !== "entregado" ? actualizarEstadoDelivery : undefined} 
      >
        <p className={estado === "entregado"? "item-status-icon status-icon--active mb-1":"item-status-icon pulse-button mb-1 shadow"}>
          <i class="bx bx-md bx-check"></i>
        </p>

        <p className={estado === "entregado"? "status-caption status-caption--active" : "status-caption"}
        >Entregado</p>
      </div>
    </div>
    </div>
  );
};

export default DeliveryStatus;
