import React from 'react'
import DatoEstadistico from '../datoEstadistico/DatoEstadistico'
import './estadisticasClientes.css'

const EstadisticasClientes = ({numClientes, cargando}) => {
    return (
        <div className="estadisticas-clientes">
            <DatoEstadistico icon={<i class='bx bx-group bx-md'></i>} cargando={cargando} number={numClientes} caption="Clientes" color="bg-ventas"/>
            
        </div>
    )
}

export default EstadisticasClientes
