import React, { useContext } from 'react'
import PosContext from '../../context/pos/posContext';
import './categoriaComponent.css'


const CategoriaComponent = ({ categoria }) => {

    
  const {establecerCategoriaGlobal, categoria_global} = useContext(PosContext);
  

    return (
        <div  className={categoria_global?.id === categoria.id? "categoria-component categoria-activate" : "categoria-component" }
        onClick={() => establecerCategoriaGlobal(categoria)}
        >
            <i class='bx bx-md bxs-dish'></i>
            <p>{categoria.nombre}</p>
            
        </div>
    )
}

export default CategoriaComponent
