import React, {useState, useContext} from 'react'
import ProductosComponent from '../components/productosComponent/ProductosComponent'
import CarritoComponent from '../components/carritoComponent/CarritoComponent'
import CategoriasComponent from '../components/categoriasComponent/CategoriasComponent'
import ConfirmarOrdenComponent from '../components/confirmarOrdenComponent/ConfirmarOrdenComponent'
import PosContext from '../context/pos/posContext'

const PuntoDeVentaScreen = () => {

    const {carrito, cliente_global} = useContext(PosContext)
    const [confirmarPedido, setConfirmarPedido] = useState(false)

    return (
        <section className="row m-0">
            <div className="col-8 pb-5">
                <CategoriasComponent/>

                <ProductosComponent />
            </div>

            <div className="col-4 m-0" style={{position: "relative"}}>

                {
                    confirmarPedido && carrito.length > 0 && cliente_global !== null ? 
                    <ConfirmarOrdenComponent setConfirmarPedido={setConfirmarPedido}/>
                    :
                    <CarritoComponent setConfirmarPedido={setConfirmarPedido} />

                }
            </div>
        </section>
    )
}

export default PuntoDeVentaScreen
