import { db } from "../firebase/firebase";

export const getCategorias = async() => {
    
    const peticion = await db.collection('categorias').get();

    if (!peticion.docs){
        console.log("ERROR AL TRAER LAS CATEGORIAS");
        return;
    }

    const data = peticion.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
    }));
    return data;

}

export const getCategoriaById = async(idCategoria) => {
    try {
        const peticion = await db.collection('categorias').doc(idCategoria).get();
        if (!peticion.exists) return;
        return peticion.data();
    } catch(error) {
    }

}



export const postCategoria = async(objCategoria) => {
    try{
        const peticion = await db.collection("categorias").add(objCategoria);
            return peticion;
        } catch (error) {
            alert("Error al crear la categoria")
            console.log("ERROR AL CREAR LA CATEGORIA: ", error);
        }
        
        
    }