import React, { useContext, useRef, useState } from "react";
import { Modal } from "react-bootstrap";
import AdministrarProductosContext from "../../context/AdministrarProductos/administrarProductosContext";
import {actualizarPlato, postPlato, subirImagenPlato } from "../../services/platosService";
import "./modalEditarProducto.css";
import Swal from "sweetalert2";
import { getCategoriaById } from "../../services/categoriaService";

const ModalEditarProducto = ({
  categorias,
  plato,
  modalEditarCliente,
  setModalEditarCliente,
}) => {
  const formPlatoOriginal = {
    nombre: plato.nombre,
    categoria_id: plato.categoria.id,
    precio: plato.precio,
    stock: plato.stock,
    id: plato.id,
    imagen: plato.imagen,
  };

  console.log(plato);

  const {setActualizarPlatos: setTraerPlatosActualizados} = useContext(AdministrarProductosContext)

  const [formPlatoEditado, setFormPlatoEditado] = useState(formPlatoOriginal);

  const [mensajeError, setMensajeError] = useState("");
  const [spinnerLoading, setSpinnerLoading] = useState(false);
  
  // VARIABLES PARA VISUALIZAR LA IMAGEN
  const [imgData, setImgData] = useState(null);
  const inputImg = useRef(null);
  // MANEJAMOS LOS CAMBIOS DE LA IMAGEN
  const handleChangeImage = (e) => {
    setMensajeError("");
    // Validamos el archivo seleccionado
    if (e.target.files[0]) {
      // instanciamos un FileReader
      const reader = new FileReader();
      // le agregamos un evento que se ejecutara cuando la imagen termine de ser leida
      reader.addEventListener("load", () => {
        setImgData(reader.result); // mostramos la lectura del archivo en el imgData
      });
      reader.readAsDataURL(e.target.files[0]); // leemos la imagen insertada como un DataUrl
    }
  };

  // MANEJAMOS LOS CAMBIOS DEL RESTO DE CAMPOS
  const handleChange = (e) => {
    setFormPlatoEditado((prevState) => ({
      ...prevState,
      [e.target.name]: e.target.value,
    }));
  };

  const validarFormulario = () => {
    setMensajeError("");

    // Validamos todos los campos
    if (formPlatoEditado.nombre.trim().length < 3) {
      setMensajeError("Escriba un nombre vlido");
      return false;
    }
    return true;
  };

  const editarPlatoStorage = async (e) => {
    e.preventDefault();
    console.log("plato", formPlatoEditado);

    // validamos los campos
    if (!validarFormulario()) return;

    Swal.fire({
      title: "Confirme para editar el plato",
      icon: "question",
      showCancelButton: true,
    }).then((rpta) => {
      if (rpta.isConfirmed) {
        setSpinnerLoading(true);
        // Validamos si hay una imagen insertada
        // si es as la subimos al storage
        if (inputImg.current.files[0]) {
          console.log("SI HAY UNA IMAGEN INSERTADA");
          subirImagenPlato(inputImg.current.files[0]).then(rpta => {
            if (rpta?.length > 0){
              console.log("Imagen creada con xito", rpta);
              // Ahora actualizamos el plato
              getCategoriaById(formPlatoEditado.categoria_id).then(categoria_nombre => {
                let objPlato = {
                  ...formPlatoEditado,
                  categoria: {
                    id: formPlatoEditado.categoria_id,
                    nombre: categoria_nombre.nombre
                  },
                  imagen: rpta
                }
                delete objPlato.categoria_id;

                actualizarPlato(formPlatoEditado.id, objPlato).then(rpta => {
                  if (rpta.length > 0) {
                    setTraerPlatosActualizados(prevState => !prevState);
                    Swal.fire({
                      title: "Plato editado con xito",
                      icon: "success", 
                      timer: 800,
                      showCancelButton: false
                    });
                    setModalEditarCliente(false);
                    // Actualizamos los platos traidos de la categoria actual, para ver los editados
                    setSpinnerLoading(false);
                    console.log("PLATO CON IMAGEN CREADA", rpta.id);
                    // Reseteamos el formulario
                    setImgData(null);
                    setFormPlatoEditado(formPlatoOriginal);
                  } else {
                    alert("Error al guardar la informacin del plato")
                    setSpinnerLoading(false);
                  }




              })

              })
            } else {
              alert("Error al guardar la imagen");
              setSpinnerLoading(false);
            }
          })
        } else {
          // SI NO HAY UNA IMAGEN INSERTADA, SOLO ACTUALIZAMOS EL PLATO
          getCategoriaById(formPlatoEditado.categoria_id).then(categoria_nombre => {

            let objPlato = {
              ...formPlatoEditado,
              categoria: {
                id: formPlatoEditado.categoria_id,
                nombre: categoria_nombre.nombre
              }
            }
            delete objPlato.categoria_id;

            actualizarPlato(formPlatoEditado.id, objPlato).then(rpta => {
              if (rpta.length > 0) {
                // PLATO ACTUALIZADO
                Swal.fire({
                  title: "Plato editado con xito",
                  icon: "success",
                  timer: 800,
                  showCancelButton: false
                });
                setModalEditarCliente(false);
                // Actualizamos los platos traidos de la categoria actual, para ver los editados
                setTraerPlatosActualizados(prevState =>  !prevState);
                setSpinnerLoading(false);
                // Reseteamos el formulario
                setFormPlatoEditado(formPlatoOriginal);
              } else {
                alert("Error al actualizar el plato");
                setSpinnerLoading(false);
              }
            })


          });  
        }
      }
    });

  };

  return (
    <>
      <Modal
        show={modalEditarCliente}
        onHide={() => {
          setModalEditarCliente(false);
        }}
        backdrop="static"
        keyboard={false}
        centered
      >
        <Modal.Header>
          <Modal.Title>Editar Plato</Modal.Title>
        </Modal.Header>

        <Modal.Body>
          <form className="ps-4 pe-4" 
          onSubmit={editarPlatoStorage}
          >
            {spinnerLoading && (
              <div className="spinner-wrapper d-flex justify-content-center align-items-center">
                <div className="spinner-border mb-5" role="status">
                  <span className="visually-hidden">Loading...</span>
                </div>
              </div>
            )}

            {/* EDITAR NOMBRE DEL PLATO */}
            <div className="row mb-3">
              <label htmlFor="name" className="form-label">
                Nombre del Plato
              </label>
              <input
                required
                type="text"
                id="name"
                name="nombre"
                className="form-control"
                value={formPlatoEditado.nombre}
                onChange={handleChange}
              />
            </div>

            {/* EDITAR CATEGORA */}
            <div className="row mb-3">
              <label htmlFor="categoria" className="form-label">
                Categora
              </label>

              <select
                id="categoria"
                name="categoria_id"
                class="form-select"
                aria-label="Selecciona la categora"
                value={formPlatoEditado.categoria_id}
                onChange={handleChange}
              >
                <option disabled selected>
                  Selecciona la categora
                </option>
                {categorias.map((categoria) => {
                  return (
                    <option key={categoria?.id} value={categoria.id}>
                      {categoria.nombre}
                    </option>
                  );
                })}
              </select>
            </div>

            {/* EDITAR PRECIO Y STOCK */}
            <div className="mb-5 row gap-3">
              <div className="col-3 p-0">
                <label htmlFor="precio" className="form-label ps-2">
                  Precio S/
                </label>
                <input
                  type="number"
                  id="precio"
                  className="form-control"
                  name="precio"
                  value={formPlatoEditado.precio}
                  onChange={handleChange}
                />
              </div>
              <div className="col-3 p-0">
                <label htmlFor="stock" className="form-label ps-2">
                  Stock
                </label>
                <input
                  type="number"
                  id="stock"
                  className="form-control"
                  name="stock"
                  value={formPlatoEditado.stock}
                  onChange={handleChange}
                />
              </div>
            </div>
            {/* INSERTAR IMAGEN */}
            <div class="mb-3">
              <label for="formFileSm" className="form-label">
                Cambiar de imgen (Opcional)
              </label>
              <input
                accept=".png, .jpg, .jpeg"
                className="form-control form-control-sm"
                id="formFileSm"
                name="imagen"
                type="file"
                ref={inputImg}
                onChange={handleChangeImage}
              />
            </div>
            {/* SECCIN DONDE SE PREVISUALIZA LA IMAGEN **********/}
            <div className="row mb-3">
              {imgData !== null ? (
                <div className="col-6">
                  <img
                    style={{ width: "100%" }}
                    src={imgData}
                    alt="foto del producto"
                  />
                </div>
              ) : (
                <div className="col-6">
                  <img
                    style={{ width: "100%" }}
                    src={formPlatoEditado.imagen}
                    alt="foto del producto"
                  />
                </div>
              )}
            </div>
            {mensajeError.length > 0 && (
              <div class="alert alert-warning" role="alert">
                {mensajeError}
              </div>
            )}
            <hr />
            <div className="mb-3 mt-4 d-flex justify-content-end gap-3">
              <button
                style={{"z-index": "1"}}
                class="btn btn-secondary"
                type="button"
                onClick={() => {
                  setMensajeError("");
                  setFormPlatoEditado(formPlatoOriginal);
                  setModalEditarCliente(false);
                  setImgData(null);
                  setSpinnerLoading(false);
                }}
              >
                Cerrar
              </button>

              <button class="btn btn-dark" type="submit">
                Editar
              </button>
            </div>
          </form>
        </Modal.Body>
      </Modal>
    </>
  );
};

export default ModalEditarProducto;
