import { db, storage } from "../firebase/firebase";

export const getPlatos = async () => {
  const peticion = await db.collection("platos").get();
  if (!peticion.docs) {
    console.log("Error al traer los platos del backend");
  }

  const data = peticion.docs.map((doc) => ({
    id: doc.id,
    ...doc.data(),
  }));

  console.log("PLATOS");
  console.log(data);

  return data;
};

export const getPlatosByCategoria = async (idCategoria) => {
  console.log("SERVICIO CATS");
  const peticion = await db
    .collection("platos")
    .where("categoria.id", "==", idCategoria)
    .get();
  if (!peticion.docs) {
    alert("Error al traer los platos por categoria");
    return;
  }
  const data = peticion.docs.map((doc) => ({
    id: doc.id,
    ...doc.data(),
  }));

  return data;
};

export const postPlato = async (objPlato) => {
    try{
        const peticion = await db.collection("platos").add(objPlato);
        return peticion;
    } catch(error) {
        console.log("ERROR AL CREAR PLATO: ", error);
    }
};

export const actualizarPlato = async (id, objPlato) => {
  try {
    const peticion = await db.collection("platos").doc(id).update(objPlato)
    return "plato actualizado"
  } catch(error) {
    console.log("Error al actualizar el plato", error);
  } 
}



