import React, { useContext, useState } from "react";
import "./carritoComponent.css";
import ItemCarrito from "../itemCarrito/ItemCarrito";
import PosContext from "../../context/pos/posContext";
import ModalCrearCliente from "../modalCrearCliente/ModalCrearCliente";
import CarritoCliente from "../carritoCliente/CarritoCliente";

const CarritoComponent = ({ setConfirmarPedido }) => {
  const { carrito, monto_total, cliente_global } = useContext(PosContext);

  return (
    <>
      <ModalCrearCliente />
      {/* =================== */}
      <div className="carrito-component ps-xxl-5 pe-xxl-5 pt-xxl-5 ps-xl-4 pe-xl-4 pt-xl-3 p-3 pb-0 shadow-sm">
        <div className="carrito-header pb-xxl-0 pb-xl-0">
          <h2 className="carrito-title mb-xxl-3 mb-xl-2">Pedido</h2>

          <CarritoCliente/>

          <div className="carrito-info mb-0 p-0">
            <p className="mb-xxl-1 mb-xl-0">Item</p>
            <p className="mb-xxl-1 mb-xl-0">Precio</p>
          </div>
        </div>

        <div className="carrito-body">
          {carrito.length > 0 ? (
            carrito.map((plato) => <ItemCarrito plato={plato} />)
          ) : (
            <p className="mt-4 fs-4" style={{"color": "text-primary-light"}}>Carrito vacio</p>
          )}
        </div>
        <div className="carrito-footer pb-4">
          <div className="carrito-total pt-xxl-3 pt-xl-2 mb-2">
            <p className="mb-1">Total</p>
            <p className="total-price mb-0">S/ {monto_total}</p>
          </div>

          <button
            onClick={() =>
              carrito.length > 0 && cliente_global !== null
                ? setConfirmarPedido(true)
                : null
            }
            className={
              carrito.length > 0 && cliente_global !== null
                ? "btn-continuar-orden"
                : "btn-continuar-orden btn-continuar-orden--disactived"
            }
          >
            Continuar
          </button>
        </div>
      </div>
    </>
  );
};

export default CarritoComponent;
