import React from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import MenuComponent from "../../components/menuComponent/MenuComponent";
import PedidosState from "../../context/pedidos/pedidosState";
import PosState from "../../context/pos/posState";
import AdministrarScreen from "../../screens/AdministrarScreen";
import ClientesScreen from "../../screens/ClientesScreen";
// import PagosScreen from "../../screens/PagosScreen";
import PedidosScreen from "../../screens/PedidosScreen";
import PuntoDeVentaScreen from "../../screens/PuntoDeVentaScreen";

const AdminRouter = () => {
  return (
    <>
      <MenuComponent />
      <div style={{ marginLeft: "var(--width-sidebar)" }}>
        <PosState>
          <PedidosState>
            <Switch>
              <Route exact path="/vender" component={PuntoDeVentaScreen} />
              <Route path="/pedidos" component={PedidosScreen} />
              {/* <Route path="/pagos" component={PagosScreen} /> */}
              <Route path="/clientes" component={ClientesScreen} />
              <Route path="/administrar" component={AdministrarScreen} />
            </Switch>
          </PedidosState>
        </PosState>
      </div>
    </>
  );
};

export default AdminRouter;
