import React from "react";
import "./datoEstadistico.css";

const DatoEstadistico = ({ icon, number, caption, color, cargando }) => {
  return (
    <div className="dato-estadistico">
      <p className={`dato-icon ${color}`}>{icon}</p>

      {cargando ? (
        <div
          class="spinner-border spinner-border-sm text-secondary"
          role="status"
        >
          <span class="visually-hidden">Loading...</span>
        </div>
      ) : (
        <div>
          <p className="dato-number">{number}</p>
          <p className="dato-info">{caption}</p>
        </div>
      )}
    </div>
  );
};

export default DatoEstadistico;
