import React, { useEffect, useState } from "react";
import PosContext from "../../context/pos/posContext";
import { getCategorias } from "../../services/categoriaService";
import CategoriaComponent from "../categoriaComponent/CategoriaComponent";
import "./categoriasComponent.css";

const CategoriasComponent = () => {
  const [categorias, setCategorias] = useState([]);
  const [cargando, setCargando] = useState(true);

  useEffect(() => {
    setCargando(true);
    getCategorias().then((data) => {
      setCategorias(data);
      setCargando(false);
    });
  }, []);

  return (
    <div className="row">
      <div className="col-12">
        <div className="row p-xxl-4 pb-xxl-2 pt-xl-4 pb-xl-2 ps-xl-3 pt-3 ps-2">
          <div className="col-xxl-5 col-7">
            <h2 className="subtitle">Elige la categora</h2>
          </div>
          <div className="col-xxl-6 col-5 ">
            {/* SEARCH PLATO */}
            {/* <form className="p-2">
              <input
                type="text"
                className="form-control"
                placeholder="Busca un plato"
              />
            </form> */}
          </div>
        </div>
      </div>

      <div className="col-12">
        {cargando ? (
          <div class="ms-xxl-4 ms-xl-3 spinner-border text-secondary" role="status">
            <span class="visually-hidden">Loading...</span>
          </div>
        ) : (
          <div className="categorias-container">
            {categorias.map((cat) => {
              return <CategoriaComponent categoria={cat} key={cat.id} />;
            })}
          </div>
        )}
      </div>
    </div>
  );
};

export default CategoriasComponent;
