import React, { useContext } from 'react'
import './productoComponent.css'
import productImg from '../../assets/images/frapuchino.png'
import PosContext from '../../context/pos/posContext'

const ProductoComponent = ({producto, accionName, accionFunction}) => {



    return (
        <div className="producto-component shadow-sm">
            <div className="producto-img">
                <img src={producto?.imagen} alt="" />
            </div>
            <div className="producto-body">
                <h3 className="producto-title mb-1 ps-1 pe-1">
                    {producto.nombre}
                </h3>
                <p className="producto-price mb-xxl-2 mb-1">
                    S/ {producto.precio}
                </p>
                {/* <div className="producto-size">
                    <p>Tamao</p>
                    <div className="size-options">
                        <p>S</p>
                        <p>M</p>
                        <p>L</p>
                    </div>
                </div> */}
                <p className="producto-stock mb-xxl-3 mb-xl-2 mb-0">
                    {producto.stock} disponibles
                </p>
            </div>
            <div className="producto-footer">
                <button 
                onClick={() => accionFunction(producto)}
                className="agregarProducto-btn mt-1">{accionName}</button>
            </div>
        </div>
    )
}

export default ProductoComponent
