import React, { useContext } from "react";
import "./estadisticasComponent.css";
import DatoEstadistico from "../datoEstadistico/DatoEstadistico";
import PedidosContext from "../../context/pedidos/pedidosContext";

const EstadisticasComponent = ({ pedidos, cargando }) => {
  let ventasTotales = pedidos?.reduce((montoVendido, pedido) => {
    return (montoVendido += pedido.monto_a_pagar);
  }, 0);

  let montoPorCobrar = pedidos?.reduce((contador, pedido) => {
    return (contador += pedido.monto_a_pagar - pedido.monto_pagado);
  }, 0);

  console.log("Estadisticas Component", pedidos)

  return (
    <div className="estadisticas-component">
      <DatoEstadistico
        cargando={cargando}
        icon={<i class="bx bxs-dish bx-md"></i>}
        number={pedidos.length}
        caption="Pedidos Totales"
        color="bg-pedidos"
      />
      <DatoEstadistico
        cargando={cargando}
        icon={<i className="bx bx-dollar-circle bx-md"></i>}
        number={`S/ ${ventasTotales}`}
        caption="Monto Vendido"
        color="bg-ventas"
      />
      <DatoEstadistico
        cargando={cargando}
        icon={<i class="bx bxs-pie-chart-alt bx-md"></i>}
        number={`S/ ${montoPorCobrar}`}
        caption="Por cobrar"
        color="bg-cobrar"
      />
    </div>
  );
};

export default EstadisticasComponent;
