import React, { useState, useEffect, useContext } from "react";
import "./administrarProductos.css";
import PlatoAdministrar from "../platoAdministrar/PlatoAdministrar";
import AdministrarProductosContext from "../../context/AdministrarProductos/administrarProductosContext";
import ModalCrearPlato from "../modalCrearPlato/ModalCrearPlato";
import ModalAdministrarCategoria from "../modalAdministrarCategoria/ModalAdministrarCategoria";

const AdministrarProductos = () => {
  const {
    categorias,
    cargandoCategorias,
    setPlatosState,
    platos,
    cargandoPlatos,
    categoriaSeleccionada,
    setCategoriaSeleccionada,
  } = useContext(AdministrarProductosContext);

  const [modalCrear, setModalCrear] = useState(false);
  const [modalCategoria, setModalCategoria] = useState(false);

  return (
    <section className="administrar-productos shadow">
      <div className="administrar-productos__header pt-xxl-5 pt-xl-4 pt-3 ps-3 pe-2 mb-xxl-4 mb-0">
        <h3 className="ps-2">Administrar Productos</h3>
        {categorias.length > 0 && (
          <div>
            <ModalAdministrarCategoria
              modalCategoria={modalCategoria}
              setModalCategoria={setModalCategoria}
              categorias={categorias}
            />
            <button
              onClick={() => setModalCategoria(true)}
              className="btn-administrar-categorias"
            >
              <i class="bx bx-cog"></i> Administrar Categorias
            </button>
          </div>
        )}
      </div>

      <nav className="administrar-productos__nav">
        {/************  CATEGORIAS PARA SELECCIONAR ************/}
        <ul className="d-flex gap-4 m-0 ps-lg-3">
          {categorias.map((categoria) => {
            return (
              <li
                key={categoria.id}
                className={
                  categoriaSeleccionada?.id === categoria.id
                    ? "administrar-productos__link p-2 administrar-productos__link--activate"
                    : "administrar-productos__link p-2"
                }
                onClick={() => {
                  console.log("CLICK CATEGORIA");
                  setPlatosState((prevState) => ({
                    cargando: true,
                    platos: [],
                  }));
                  setCategoriaSeleccionada(categoria);
                }}
              >
                {categoriaSeleccionada?.id === categoria?.id
                  ? `${categoria.nombre} (${platos.length})`
                  : categoria.nombre}
                {/* {categoria.nombre}  */}
              </li>
            );
          })}
        </ul>
      </nav>

      <div className="administrar-productos__body ps-xxl-3 pe-xxl-3 ps-0 p-0">
        {/*============> MODAL PARA CREAR PLATOS <==================*/}
        <ModalCrearPlato
          modalCrear={modalCrear}
          setModalCrear={setModalCrear}
        />
        <div className="administrar-productos__container p-xxl-5 p-4">
          {categoriaSeleccionada !== null && cargandoPlatos === false && (
            <div
              onClick={() => {
                setModalCrear(true);
              }}
              className="crear-plato-btn"
            >
              <i class="bx bx-md bx-plus"></i>
              Crear nuevo plato
            </div>
          )}

          {categoriaSeleccionada !== null ? (
            !cargandoPlatos ? (
              platos.map((plato) => {
                return (
                  <PlatoAdministrar
                    key={plato.id}
                    plato={plato}
                    categorias={categorias}
                  />
                );
              })
            ) : (
              <div class="spinner-border text-secondary" role="status">
                <span class="visually-hidden">Loading...</span>
              </div>
            )
          ) : (
            <div class="alert alert-info fs-4 m-4" role="alert">
              Selecciona una categora
            </div>
          )}
        </div>
        {/* <ProductoComponent/>
                <ProductoComponent/>
                <ProductoComponent/> */}
      </div>
    </section>
  );
};

export default AdministrarProductos;
