import React, { useState, useContext, useRef, useEffect } from "react";
import { Modal } from "react-bootstrap";
import AdministrarProductosContext from "../../context/AdministrarProductos/administrarProductosContext";
import Swal from "sweetalert2";
import {
  actualizarPlatos,
  postPlato,
  subirImagenPlato,
} from "../../services/platosService";
import "./modalCrearPlato.css";

const ModalCrearPlato = ({ modalCrear, setModalCrear }) => {
  const { setActualizarPlatos, categoriaSeleccionada } = useContext(
    AdministrarProductosContext
  );

  console.log("CATEGORIA: ", categoriaSeleccionada);

  const formPlatoVacio = {
    nombre: "",
    precio: 0,
    stock: 1,
    categoria: categoriaSeleccionada,
  };
  const [formPlatoCrear, setformPlatoCrear] = useState(formPlatoVacio);
  const [spinnerLoading, setSpinnerLoading] = useState(false)

  useEffect(() => {
    if (categoriaSeleccionada) {
      console.log("ACTUALIZANDO CATEGORIA >>>>>");
      setformPlatoCrear((prevState) => ({
        ...prevState,
        categoria: categoriaSeleccionada,
      }));
    }
  }, [categoriaSeleccionada]);

  // const { crearPlato } = useContext(AdministrarProductosContext);
  const [mensajeError, setMensajeError] = useState("");
  const inputImg = useRef(null);
  let img = null;

  // SETEAR LA CATEGORIA SELECCIONADA COMO CATEGORIA DEL PLATO, CUANDO ESTA SE CARGUE
  // useEffect(() => {
  //   setformPlatoCrear((prevState) => ({
  //     ...prevState,
  //     categoria_id: categoriaActualCrearPlato?.id,
  //   }));
  //   console.log("categoriaActualCrearPlato" , categoriaActualCrearPlato);
  // }, [categoriaActualCrearPlato]);

  // VARIABLE PARA VISUALIZAR LA IMAGEN ********************
  const [imgData, setImgData] = useState(null);

  // MANEJAMOS LOS CAMBIOS EN LA IMAGEN
  const handleChangeImage = (e) => {
    setMensajeError("");
    // validamos el primer archivo
    if (e.target.files[0]) {
      // seteamos la imagen seleccionada
      console.log("img: ", inputImg.current.files[0]);
      console.log("e.target.files[0]:  ", e.target.files[0]);
      const reader = new FileReader();
      // cuando la lectura se complete setearemos el resultado del reader como valor que se previsualizara
      /* el atributo "result" contiene la informacin URL representando la informacin del archivo como una 
       cadena de caracteres codificados en base64 */
      reader.addEventListener("load", () => {
        setImgData(reader.result);
        // setSubmitActivado(true);
      });
      //
      // leemos el contenido
      reader.readAsDataURL(e.target.files[0]);
    }
  };

  // MANEJAMOS LOS CAMBIOS DE LOS DEMAS INPUTS
  const handleChange = (e) => {
    setMensajeError("");
    setformPlatoCrear((prevState) => ({
      ...prevState,
      [e.target.name]: e.target.value,
    }));
  };

  const validarFormuralio = () => {
    setMensajeError("");

    // Validamos todos los campos
    if (formPlatoCrear.nombre.trim().length < 3) {
      setMensajeError("Escriba un nombre vlido");
      return false;
    }

    if (imgData === null) {
      setMensajeError("Inserte una imgen del producto");
      return false;
    }

    return true;
  };


  const crearPlatoStorage = (e) => {
    console.log("plato: ", formPlatoCrear);

    e.preventDefault();

    // validamos los campos
    if (!validarFormuralio()) return;

    Swal.fire({
      title: "Confirme para crear el plato",
      icon: "question",
      showCancelButton: true,
    }).then((rpta) => {
      if (rpta.isConfirmed) {
        console.log("PLATO A CREAR >>>", formPlatoCrear);
        setSpinnerLoading(true);
        // Primero subimos la imagen
        subirImagenPlato(inputImg.current.files[0]).then((rpta) => {
          // console.log("Respuesta al crear la imagen 3========");
          if (rpta?.length > 0) {
            console.log("Imagen creada con exito", rpta);
            // Ahora creamos el plato
            postPlato({
              ...formPlatoCrear,
              imagen: rpta,
            }).then((rpta) => {
              if (rpta.id) {
                Swal.fire({
                  title: "Plato creado con xito",
                  icon: "success",
                  timer: 800,
                  showCancelButton: false,
                });
                setModalCrear(false);
                setActualizarPlatos((prevState) => !prevState);
                setSpinnerLoading(false);
                console.log("PLATO CON IMAGEN CREADA", rpta.id);
                // Reseteamos el formulario
                setImgData(null);
                setformPlatoCrear(formPlatoVacio);
              } else {
                alert("Error al guardar la informacin del plato");
                setSpinnerLoading(false);
              }
            });
          } else {
            alert("Error al guardar la imagen");
            setSpinnerLoading(false);
          }
        });
      }
    });
  };

  return (
    <Modal
      show={modalCrear}
      onHide={() => {
        setModalCrear(false);
      }}
      backdrop="static"
      keyboard={false}
      centered
    >
      <Modal.Header>
        <Modal.Title>Crear Plato</Modal.Title>
      </Modal.Header>

      <Modal.Body>
        <form
          className="ps-4 pe-4 form-crear-producto"
          onSubmit={crearPlatoStorage}
        >
          {spinnerLoading && (
            <div className="spinner-wrapper d-flex justify-content-center align-items-center">
              <div className="spinner-border mb-5" role="status">
                <span className="visually-hidden">Loading...</span>
              </div>
            </div>
          )}

          {/* CREAR NOMBRE DEL PLATO */}
          <div className="row mb-3">
            <label htmlFor="name" className="form-label">
              Nombre del Plato
            </label>
            <input
              type="text"
              id="name"
              name="nombre"
              className="form-control"
              value={formPlatoCrear.nombre}
              onChange={handleChange}
            />
          </div>
          {/* ELEGIR CATEGORA */}
          <div className="row mb-3">
            <label htmlFor="categoria" className="form-label">
              Categora
            </label>

            <select
              id="categoria"
              name="categoria_id"
              class="form-select"
              aria-label="Selecciona la categora"
              value={formPlatoVacio.categoria_id}
            >
              <option selected>{categoriaSeleccionada?.nombre}</option>
              {/* {categorias.map((categoria) => {
                return (
                  <option key={categoria.id} value={categoria.id}>
                    {categoria.nombre}
                  </option>
                );
              })} */}
            </select>
          </div>
          {/* INGRESAR PRECIO Y STOCK */}
          <div className="mb-5 row gap-3">
            <div className="col-3 p-0">
              <label htmlFor="precio" className="form-label ps-2">
                Precio S/
              </label>
              <input
                type="number"
                min="0"
                id="precio"
                className="form-control"
                name="precio"
                value={formPlatoCrear.precio}
                onChange={handleChange}
              />
            </div>
            <div className="col-3 p-0">
              <label htmlFor="stock" className="form-label ps-2">
                Stock
              </label>
              <input
                type="number"
                id="stock"
                min="1"
                className="form-control"
                name="stock"
                value={formPlatoCrear.stock}
                onChange={handleChange}
              />
            </div>
          </div>
          {/* INSERTAR IMAGEN */}
          <div class="mb-3">
            <label for="formFileSm" className="form-label">
              Selecciona una imgen
            </label>
            <input
              accept=".png, .jpg, .jpeg"
              className="form-control form-control-sm"
              id="formFileSm"
              name="imagen"
              type="file"
              onChange={handleChangeImage}
              ref={inputImg}
            />
          </div>
          {/* SECCION DONDE SE PREVISUALIZA LA IMAGEN ************ */}
          {imgData !== null && (
            <div>
              <img width="200" src={imgData} alt="foto del producto" />
            </div>
          )}
          {/* *********************************************** */}
          {mensajeError.length > 0 && (
            <div class="alert alert-warning" role="alert">
              {mensajeError}
            </div>
          )}
          <hr />
          <div className="mb-3 mt-4 d-flex justify-content-end gap-3">
            <button
              type="button"
              style={{ "z-index": "1" }}
              onClick={() => {
                setMensajeError("");
                setformPlatoCrear(formPlatoVacio);
                setImgData(null);
                setModalCrear(false);
                setSpinnerLoading(false);
              }}
            >
              Cerrar
            </button>

            <button type="submit">Crear Plato</button>
          </div>
        </form>
      </Modal.Body>
    </Modal>
  );
};

export default ModalCrearPlato;
