import React, { useContext, useEffect, useState } from "react";
import PosContext from "../../context/pos/posContext";
import { getClientes } from "../../services/clientesService";
import './clientesSearch.css'

const ClientesSearch = () => {
  const [searchWord, setSearchWord] = useState("");
  const [sugerencias, setSugerencias] = useState([]);
  const [clientes, setClientes] = useState([]);

  const { establecerClienteGlobal } = useContext(PosContext);

  useEffect(() => {
    getClientes().then((data) => {
      setClientes(data);
    });
  }, []);

  // Funcion que setea el cliente global
  const setClienteGlobal = (cliente) => {
    establecerClienteGlobal(cliente)
    setSearchWord("")
    setSugerencias([])
  };

  const handleChange = (e) => {
    // BUSCADOR DEL CLIENTE: 
    // Busca coincidencias con la base de datos clientes conforme se va escribiendo el nombre
    let result = [];
    if (e.target.value.length > 0) {
      let regex = new RegExp(`${e.target.value.trim()}`, "i");
      result = clientes.sort().filter((cliente) => regex.test(cliente.nombre + " " + cliente.apellido));
    }
    setSugerencias(result);
    setSearchWord(e.target.value);
  };

  return (
    <div className="cliente-search d-flex align-items-center w-100">
      <i class='bx bx-search-alt bx-sm pt-2 pe-1 ps-1'></i>
      <input
        className="input-cliente-search"
        type="text"
        value={searchWord}
        onChange={handleChange}
        placeholder="Buscar cliente"
      />
      <ul className="lista-clientes-search shadow-sm">
        {searchWord.length > 0 ? (
          sugerencias.length > 0 ? (
            sugerencias.map((cliente, i) => {
              return (
                <li
                 className="cliente-search-item p-2 ps-3"
                 onClick={() => setClienteGlobal(cliente)} key={i}>
                  {`${cliente.nombre} ${cliente.apellido}`}
                </li>
              );
            })
          ) : (
            <p className="alert alert-info">No hay clientes con ese nombre</p>
          )
        ) : null}
      </ul>
    </div>
  );
};

export default ClientesSearch;
