import React from 'react'
import './buttonAgregarCliente.css'

const ButtonAgregarCliente = ({ setModalCrearCliente }) => {

    return (
        <>
          {/* <p className="tooltip-crear-cliente p-2 fs-6" >Crear cliente nuevo</p> */}
          <button
          variant="success"
            className="d-flex align-items-center justify-content-center btn-crear-cliente"
            onClick={() => setModalCrearCliente(true)}
          >
            <i class='bx bx-plus-circle bx-md'></i>
          </button>
          </>
    )
}

export default ButtonAgregarCliente
