import React, { useEffect, useState } from "react";
import { Modal } from "react-bootstrap";
import { getClienteById } from "../../services/clientesService";
import { getPedidos } from "../../services/pedidosService";
import { MDBDataTableV5 } from "mdbreact";

const ModalListaPedidosByCliente = ({
  activarModalVerPedidos,
  setActivarModalVerPedidos,
  idCliente,
  setIdPedidoDetalle,
  setActivarModalDetallePedido,
  
}) => {
  const [cliente, setCliente] = useState(null);
  const [pedidos, setPedidos] = useState([]);
  const [cargandoCliente, setCargandoCliente] = useState(true);
  const [cargandoPedidos, setCargandoPedidos] = useState(true);

  useEffect(() => {
    setCargandoCliente(true);
    setCargandoPedidos(true);
    getClienteById(idCliente).then((data) => {
      console.log("CLIENTE");
      console.log(data);
      setCliente(data);
      setCargandoCliente(false);
    });
    getPedidos().then((pedidos) => {
      console.log("TODOS LOS PEDIDOS: ", pedidos);
      let filtrarPedidos = pedidos.filter(
        (pedido) => pedido.cliente.id === idCliente
      );
      console.log("FILTRADOS: ", filtrarPedidos);
      setPedidos(filtrarPedidos);
      setCargandoPedidos(false);
    });
  }, []);

  let dataTable = {
    columns: [
      { label: "Fecha", field: "fecha" },
      { label: "Platos", field: "cantidad_platos" },
      { label: "Total", field: "total" },
      { label: "Entrega", field: "entrega" },
      { label: "Estado", field: "estado" },
      { label: "Ver Detalle", field: "ver_detalle" },
    ],
    rows: [
      {
        fecha: "",
        cantidad_platos: "",
        total: "",
        entrega: "",
        estado: "",
        ver_detalle: "",
      },
    ],
  };

  const cantidadPlatos = (pedido) => {
    let cantidad = pedido.platos.reduce(
      (contador, actual) => (contador += actual.cantidad),
      0
    );
    return cantidad;
  };

  // Cuando los pedidos del cliente terminen de cargar, armamos las filas usando los datos
  let filas = [];
  if (cargandoPedidos === false) {
    console.log("PEDIDOS CARGADOS");
    filas = pedidos.map((pedido) => {
      return {
        fecha: pedido.fecha,
        cantidad_platos: cantidadPlatos(pedido),
        total: `S/ ${pedido.monto_a_pagar}`,
        entrega: pedido.estado.replace(/\w/, c => c.toUpperCase()),
        estado:
          pedido.monto_a_pagar === pedido.monto_pagado
            ? "Pago completo"
            : `Debe S/ ${pedido.monto_a_pagar - pedido.monto_pagado}`,
        ver_detalle: (
          <button
            className="btn btn-table-brown"
            onClick={() => {
              setActivarModalVerPedidos(false);
              setIdPedidoDetalle(pedido.id);
              setActivarModalDetallePedido(true);
            }}
          >
            Ver detalle
          </button>
        ),
      };
    });

    console.log("Filas: ", filas);
    dataTable = {
      ...dataTable,
      rows: filas,
    };
  }

  return (
    <Modal
      show={activarModalVerPedidos}
      onHide={() => {
        setActivarModalVerPedidos(false);
      }}
      keyboard={false}
      centered
      size="lg"
    >
      <Modal.Header>
        <Modal.Title>Pedidos del cliente</Modal.Title>
      </Modal.Header>

      <Modal.Body>
        <div className="ms-4 me-4 mt-2">
          {cargandoCliente ? (
            <div className="text-center">
              <div class="spinner-border text-secondary" role="status">
                <span class="visually-hidden">Loading...</span>
              </div>
            </div>
          ) : (
            <div className="info-cliente mb-5">
              <p>
                Cliente:{" "}
                <span className="ms-1 fs-5" style={{ fontWeight: "500" }}>
                  {cliente?.nombre + " " + cliente?.apellido}
                </span>{" "}
              </p>
              <p>
                Celular:{" "}
                <span className="ms-1 fs-5" style={{ fontWeight: "400" }}>
                  {cliente?.celular?.length > 0
                    ? cliente.celular
                    : "----------"}
                </span>{" "}
              </p>
            </div>
          )}

          <hr />

          {cargandoPedidos ? (
            <div className="text-center">
              <div class="spinner-border text-secondary" role="status">
                <span class="visually-hidden">Loading...</span>
              </div>
            </div>
          ) : (
            <div>
              <p className="fs-6" style={{ fontWeight: "500" }}>
                Lista de Pedidos
              </p>
              <MDBDataTableV5
                searching={false}
                sortable={false}
                info={false}
                paging={false}
                data={dataTable}
              />
            </div>
          )}
        </div>
      </Modal.Body>

      <Modal.Footer>
        <button
          className="btn btn-table-secondary"
          onClick={() => setActivarModalVerPedidos(false)}
        >
          Cerrar
        </button>
      </Modal.Footer>
    </Modal>
  );
};

export default ModalListaPedidosByCliente;
