import React, { useEffect, useState, useContext } from "react";
import PedidosContext from "../../context/pedidos/pedidosContext";
import { getPlatos } from "../../services/platosService";
import MostOrderedItem from "../mostOrderedItem/MostOrderedItem";
import "./mostCommon.css";

const MostCommonComponent = ({ title, pedidos }) => {

  const [platos, setPlatos] = useState([]);
  const [cargando, setCargando] = useState(true);

  useEffect(() => {
    getPlatos().then((data) => {
      setPlatos(data);
      setCargando(false);
    });
  }, []);


  const platosMasVendidos = () => {
    console.log("PEDIDOS: ", pedidos);

    console.log("PLATOS: ", platos);

    let platosYCantidades = {};
    console.log("PLATOS Y CANTIDADES: ", platosYCantidades);

    // Buscamos las cantidade vendidas de cada plato
    pedidos.forEach((pedido) => {
      pedido.platos.forEach((plato) => {
        if (platosYCantidades.hasOwnProperty(plato.plato_id)) {
          // Si el plato ya se encuentra, actualizamos su valor
          platosYCantidades[plato.plato_id] =
            platosYCantidades[plato.plato_id] + plato.cantidad;
        } else {
          // Si no encuentra, establecemos su valor inicial
          platosYCantidades[plato.plato_id] = plato.cantidad;
        }
      });
    });

    // Fltramos los platos que tienen cantidades vendidas
    let listaPlatosVendidos = platos.filter((plato) => platosYCantidades.hasOwnProperty(plato.id));

    
    //Armamos los objPlatos con sus respectivas cantidades vendidas
    listaPlatosVendidos = listaPlatosVendidos
    .map((plato) => ({
      ...plato,
      cantidad_vendida: platosYCantidades[plato.id],
    }))
    .sort((a, b) => b.cantidad_vendida - a.cantidad_vendida);
    
    return listaPlatosVendidos;
  };

  return (
    <div className="most-common-component ms-xxl-3 ms-xl-0 mt-xl-0 p-2 pt-3 ps-4 mt-3 ms-md-3 shadow">
      <div className="most-common__header">
        <h4>{title}</h4>
      </div>

      <ul className="most-common__body p-0">
        {cargando ? (
          <div className="text-center">
            <div class="spinner-border text-secondary" role="status">
              <span class="visually-hidden">Loading...</span>
            </div>
          </div>
        ) : (
          pedidos.length > 0 &&
          platos.length > 0 &&
          platosMasVendidos().map((plato) => {
            return <MostOrderedItem key={plato.id} plato={plato} />;
          })
        )}
      </ul>
    </div>
  );
};

export default MostCommonComponent;
