import React, {useContext, useState, useEffect} from "react";
import ReportePedidosComponent from "../components/reportePedidosComponent/ReportePedidosComponent";
import EstadisticasComponent from "../components/estadisticasComponent/EstadisticasComponent";
import MostCommonComponent from "../components/mostCommonComponent/MostCommonComponent";
import PedidosState from "../context/pedidos/pedidosState";
import { getPedidos } from "../services/pedidosService";

const PedidosScreen = () => {

  const [pedidos, setPedidos] = useState([])
  const [cargando, setCargando] = useState(true)

  const [actualizarPedidos, setActualizarPedidos] = useState(false);

  useEffect(() => {
    setCargando(true);
    getPedidos().then(data => {
      setPedidos(data);
      setCargando(false)
    })
  }, [actualizarPedidos])


  return (
    

    <section className="row p-xxl-4 pb-xxl-2 pt-xl-4 ps-xl-4 pb-xl-2 p-2 pt-3 m-0">
      <div className="col-xl-9 col-12">

        <h2 className="title">Pedidos</h2>

        <div className="col-12 pt-xxl-4 pt-2">
        <EstadisticasComponent cargando={cargando} pedidos={pedidos} />
        </div>

        <div className="col-12">
        <ReportePedidosComponent pedidos={pedidos} cargando={cargando} setActualizarPedidos={setActualizarPedidos}/>
        </div>


      </div>

      <div className="col-xl-3 col-4 p-0">
        <MostCommonComponent title="Productos ms vendidos" pedidos={pedidos} cargando={cargando}/>
      </div>
    </section>
    
  );
};

export default PedidosScreen;
