import { db } from "../firebase/firebase";

export const getClientes = async() => {
    const peticion = await db.collection('clientes').get();
    if (!peticion.docs){
        console.log("ERROR AL CONSUMIR LISTA DE CLIENTES");
        return;
    }
    const data = peticion.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
    }))
    return data;
}

export const getClienteById = async(idCliente) => {
    try{
        const peticion = await db.collection('clientes').doc(idCliente).get();
        if (!peticion.exists)return;
        return peticion.data();
    } catch(error){
        console.log("Error al traer el cliente por su id: ", error);
    }
}

export const updateCliente = async(idCliente, objCliente) => {
    try{
        const peticion = await db.collection('clientes').doc(idCliente).update(objCliente);
        return "pedido actualizado"
    } catch(error){
        console.log("Error al actualizar el cliente");
    }
}

export const postCliente = async(objCliente) => {
    const peticion = await db.collection('clientes').add(objCliente)
    if (!peticion.id){
        console.log("ERROR AL CREAR CLIENTE");
        return
    }
    return peticion;
}