import React, { useEffect, useState } from "react";
import AuthContext from "./authContext";
import { auth as authFirebase } from "../../firebase/firebase";
import Swal from 'sweetalert2';
import { useHistory } from 'react-router-dom';

const AuthState = ({ children }) => {

    const history = useHistory();

  const [auth, setAuth] = useState({
    usu_nom: null,
    usu_email: null,
    usu_id: null,
    isAuthenticated: null,
    cargando: true,
  });

  const iniciarSesionContext = (objUser) => {
    // Gudarmos el token del usuario actual en el localstorage
    localStorage.setItem("token", objUser.uid);

    //Seteamos el usuario en la variable de estado
    setAuth({
      usu_nom: objUser.displayName,
      usu_email: objUser.email,
      usu_id: objUser.uid,
      isAuthenticated: true,
      cargando: false,
    });
  };

  const iniciarSesionConFirebaseState = () => {
      console.log("iniciarSesionConFirebaseState");
    authFirebase.onAuthStateChanged((objUser) => {
      if (objUser) {
        // USUARIO LOGUEADO
        console.log("USUARIO LOGUEADO");
        setAuth({
          usu_nom: objUser.displayName,
          usu_email: objUser.email,
          usu_id: objUser.uid,
          isAuthenticated: true,
          cargando: false,
        });
      } else {
        // USUARIO SIN LOGUEAR
        console.log("USUARIO SIN LOGUEAR");
        setAuth({
          usu_nom: null,
          usu_email: null,
          usu_id: null,
          isAuthenticated: null,
          cargando: false,
        });
      }
    });
  };

  const cerrarSesion = () => {
    // Borramos la sesin del firebase state
    Swal.fire({
        title: "Cerrar Sesin",
        text: "Desea cerrar sesin?",
        icon: "warning",
        showCancelButton: true
    }).then(rpta => {
        if (rpta.isConfirmed) {
            authFirebase.signOut();
            history.push("/login");
        }
    })
  };

  useEffect(() => {
      iniciarSesionConFirebaseState();
  }, []);

  return (
    <AuthContext.Provider
      value={{
        usu_nom: auth.usu_nom,
        usu_email: auth.email,
        usu_id: auth.usu_id,
        isAuthenticated: auth.isAuthenticated,
        cargando: auth.cargando,
        iniciarSesionContext,
        cerrarSesion
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

export default AuthState;
