import React, { useContext, useState } from "react";
import AuthContext from "../context/auth/authContext";
import { auth } from "../firebase/firebase";

const LoginScreen = (props) => {
  const { iniciarSesionContext } = useContext(AuthContext);

  const formVacio = {
    email: "",
    password: "",
  };

  const [form, setForm] = useState(formVacio);
  const [cargando, setCargando] = useState(false);
  const [mensajeError, setMensajeError] = useState(null);

  const handleChange = (e) => {
    setMensajeError(null);
    setForm({
      ...form,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setCargando(true);
    auth
      .signInWithEmailAndPassword(form.email, form.password)
      .then((rpta) => {
        if (rpta.user) {
          setCargando(false);
          // Login exitoso
          console.log("Login exitoso");
          console.log(rpta.user);
          iniciarSesionContext(rpta.user);
          props.history.push("/vender");
        }
      })
      .catch((error) => {
        console.log("Error", error);
        if (error.code === "auth/wrong-password") {
          setMensajeError("La contrasea es invlida o el usuario no existe");
          setCargando(false);
        }
      });
  };

  return (
    <div className="row gap-0 login-screen-container">
      <div className="col-6 d-flex justify-content-center align-items-center">
        <div className="form-login-container">
          <h2 className="mb-xxl-5 mb-xl-4">Login</h2>
          {mensajeError && (
            <div class="alert alert-danger" role="alert">
              {mensajeError}
            </div>
          )}
          <form className="" onSubmit={handleSubmit}>
            {cargando ? (
              <div class="spinner-border text-secondary" role="status">
                <span class="visually-hidden">Loading...</span>
              </div>
            ) : (
              <div>
                <div className="mb-3">
                  <label htmlFor="inputEmail" className="form-label fs-6">
                    Email
                  </label>
                  <input
                    id="inputEmail"
                    className="form-control form-control-sm rounded-pill p-2 ps-3"
                    type="email"
                    placeholder="Correo electrnico"
                    value={form.email}
                    onChange={handleChange}
                    name="email"
                  />
                </div>
                <div className="mb-xxl-5 mb-xl-4">
                  <label htmlFor="inputPassword" className="form-label fs-6">
                    Password
                  </label>
                  <input
                    id="inputPassword"
                    className="form-control form-control-sm rounded-pill p-2 ps-3"
                    type="password"
                    placeholder="Contrasea"
                    value={form.password}
                    onChange={handleChange}
                    name="password"
                  />
                </div>
                <div className="d-grid">
                  <button
                    className="btn btn-primary fs-5 rounded-pill p-2"
                    type="submit"
                  >
                    Ingresar
                  </button>
                </div>
              </div>
            )}
          </form>
        </div>
      </div>
      <div className="col-6 bg-login-container"></div>
    </div>
  );
};

export default LoginScreen;
