import React from "react";
import "./perfilClienteCarrito.css";

const PerfilClienteCarrito = ({
  cliente_global,
  deseleccionarClienteGlobal,
}) => {
  return (
    <div className="perfilCliente d-flex justify-content-between align-items-center shadow p-2 rounded-pill mb-3">
      <div className="d-flex justify-content-start align-items-center">
        <i class="icon-cliente bx bxs-user-circle bx-md me-1"></i>
        <p className="perfilCliente-name">{cliente_global.nombre} {cliente_global.apellido}</p>
      </div>

      <button 
       className="btn-delete-perfil-cliente"
       onClick={deseleccionarClienteGlobal}>
      <i class='bx bx-x'></i>
      </button>
    </div>
  );
};

export default PerfilClienteCarrito;
