import React from "react";
import { NavLink } from "react-router-dom";
import "./settings.css";

const SettingsMenu = () => {
  return (
    <div className="settings-menu shadow-sm">
      <ul>
        <li>
          <NavLink
            activeClassName="settings-menu-active"
            className="settings-menu__link"
            to="/administrar/productos"
          >
            <div className="settings-menu__icon">
              <i class="bx bx-store bx-md"></i>
            </div>

            <div className="settings-menu__info">
              <p className="settings-menu__title">Administrar productos</p>
            </div>
          </NavLink>
        </li>
        <li>
          <NavLink
            activeClassName="settings-menu-active"
            className="settings-menu__link"
            to="/administrar/seguridad"
          >
            <div className="settings-menu__icon">
              <i class="bx bx-store bx-md"></i>
            </div>

            <div className="settings-menu__info">
              <p className="settings-menu__title">Seguridad</p>
            </div>
          </NavLink>
        </li>

        
      </ul>
    </div>
  );
};

export default SettingsMenu;
