import React, { useState, useEffect } from "react";
import { Modal } from "react-bootstrap";
import { getClienteById, updateCliente } from "../../services/clientesService";
import Swal from 'sweetalert2'

const ModalEditarCliente = ({
  activarModalEditarCliente,
  setActivarModalEditarCliente,
  idCliente,
  setActualizadorClientes
}) => {
  const formVacio = {
    nombre: "",
    apellido: "",
    celular: null,
  };

  console.log("ID CLIENTE MODAL: ", idCliente);

  const [cliente, setCliente] = useState({});
  const [cargando, setCargando] = useState(true);
  const [formCliente, setFormCliente] = useState(formVacio);
  const [mensajeError, setMensajeError] = useState(null);
  const [activarBtnEditar, setActivarBtnEditar] = useState(false);
  const [actualizadorCliente, setActualizadorCliente] = useState(false)

  useEffect(() => {
    if (idCliente === null) return;
    getClienteById(idCliente).then((data) => {
      setFormCliente({
        ...data,
        id: idCliente,
      });
      setCargando(false);
      setCliente({
        ...data,
        id: idCliente,
      });
    });
  }, [actualizadorCliente]);

  const handleChange = (e) => {
    setActivarBtnEditar(true);
    setMensajeError(null);
    setFormCliente((prevState) => ({
      ...prevState,
      [e.target.name]: e.target.value,
    }));
  };

  const validarFormulario = () => {
    setMensajeError(null);
    if (
      formCliente.nombre.trim() === cliente.nombre &&
      formCliente.apellido.trim() === cliente.apellido &&
      formCliente.celular?.trim() === cliente.celular
    ) {
      console.log(formCliente.celular, cliente.celular);
      setMensajeError("Ningn campo modificado");
      return false;
    }
    if (formCliente.nombre.trim().length < 3) {
      setMensajeError("Escribe un nombre vlido");
      return false;
    }
    if (formCliente.apellido.trim().length < 3) {
      setMensajeError("Escribe un apellido vlido");
      return false;
    }

    return true;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!validarFormulario()) return;
    Swal.fire({
        title: "Desea editar el cliente?",
        icon: "question", 
        showCancelButton: true
    }).then(rpta => {
        if (rpta.isConfirmed) {
            setCargando(true);
            updateCliente(idCliente, formCliente).then(rpta => {
                if (rpta?.length > 0) {
                    setCargando(false);
                    setActualizadorCliente(prevState => !prevState)
                    setActualizadorClientes(prevState => !prevState)
                    Swal.fire({
                        title: "Cliente editado",
                        icon: "success", 
                        showCancelButton: false,
                        timer: 800
                    })
                    setActivarModalEditarCliente(false)
                }
            })
        }
    })
  };

  return (
    <Modal
      show={activarModalEditarCliente}
      onHide={() => {
        setActivarModalEditarCliente(false);
      }}
      size="md"
      backdrop="static"
      keyboard={false}
      centered
    >
      <Modal.Header>
        <Modal.Title>Editar Cliente</Modal.Title>
      </Modal.Header>

      <Modal.Body>
        <form onSubmit={handleSubmit} className="ms-xxl-5 me-xxl-5 ms-xl-4 me-xl-4">
          {cargando ? (
            <div className="text-center">
              <div class="spinner-border text-secondary" role="status">
                <span class="visually-hidden">Loading...</span>
              </div>
            </div>
          ) : (
            <div className="form__conn">
              <div className="row">
                <div className="mb-3">
                  <label className="form-label" htmlFor="">
                    Nombre
                  </label>
                  <input
                    onChange={handleChange}
                    name="nombre"
                    value={formCliente.nombre}
                    className="form-control"
                    type="text"
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label" htmlFor="">
                    Apellido
                  </label>
                  <input
                    onChange={handleChange}
                    name="apellido"
                    value={formCliente.apellido}
                    className="form-control"
                    type="text"
                  />
                </div>
                <div className="col-6 mb-3">
                  <label className="form-label" htmlFor="">
                    Celular (Opcional)
                  </label>
                  <input
                    onChange={handleChange}
                    name="celular"
                    value={formCliente.celular}
                    className="form-control"
                    type="text"
                  />
                </div>
              </div>
              {mensajeError ? (
                <div class="alert alert-info" role="alert">
                  {mensajeError}
                </div>
              ) : undefined}
              <hr />
              <div className="mt-4 d-flex justify-content-end gap-3">
                <button
                  type={activarBtnEditar ? "submit" : "button"}
                  className={
                    activarBtnEditar ? "btn btn-table-brown" : "btn btn-ligth"
                  }
                >
                  Editar
                </button>

                <button
                  type="button"
                  onClick={() => {
                    //   setCargando(true);
                    setFormCliente(formVacio);
                    setActivarModalEditarCliente(false);
                  }}
                  className="btn btn-table-secondary"
                >
                  Cerrar
                </button>
              </div>
            </div>
          )}
        </form>
      </Modal.Body>
    </Modal>
  );
};

export default ModalEditarCliente;
