import React, { useState, useContext } from "react";
import { Modal } from "react-bootstrap";
import { postCategoria } from "../../services/categoriaService";
import Swal from 'sweetalert2'
import AdministrarProductosContext from "../../context/AdministrarProductos/administrarProductosContext";
import './modalAdministrarCategoria.css'


const ModalAdministrarCategoria = ({
  modalCategoria,
  setModalCategoria,
  categorias,
}) => {

  const {setActualizarCategorias} = useContext(AdministrarProductosContext)

  const [modoEdicion, setModoEdicion] = useState(false);
  console.log("MODAL CATEGORIA");
  console.log(modalCategoria);



  const editarCategoria = () => {

  }

  //  FUNCIONALIDAD CREAR CATEGORIA
  const [inputCrearCategoria, setInputCrearCategoria] = useState("")

  const handleChangeCrearCategoria = (e) => {
    setInputCrearCategoria(e.target.value);
  } 

  const crearCategoria = () => {
    if(inputCrearCategoria.trim().length > 3) {
       Swal.fire({
         title: "Confirme para crear la categora",
         icon: "question",
         showCancelButton: true

       }).then(rpta => {
         if (rpta.isConfirmed){
           try{
             postCategoria({nombre: inputCrearCategoria}).then(rpta => {
               if (rpta?.id) {
                 console.log("Categoria Creada >>>>>>>>>");
                 Swal.fire({
                   title: "Categoria creada",
                   icon: "success", 
                   timer: 800,
                   showCancelButton: false
                 })
                 setInputCrearCategoria("");
                 setActualizarCategorias(prevState => !prevState)
               }
               else {
                 alert("Error al crear la categoria")
               }
             })
           } catch(error) {
             console.log("ERROR AL CREAR LA CATEGORIA: ", error);
           }

         }
       })
    }
  }


  return (
    <Modal
      show={modalCategoria}
      onHide={() => {
        setModalCategoria(false);
      }}
      backdrop="static"
      keyboard={false}
      centered
    >
      <Modal.Header>
        <div className="d-flex w-100 justify-content-between align-items-center">
          <Modal.Title>Administrar Categorias</Modal.Title>
          <button
            style={{
              background: "transparent",
              border: "none",
              outline: "none",
            }}
            onClick={() => setModalCategoria(false)}
          >
            <i class="bx bx-sm bx-x"></i>
          </button>
        </div>
      </Modal.Header>

      <Modal.Body>
        <ul className="w-100 m-0 p-0 mb-5">
          {categorias.map((categoria) => {
            return (
              <li className="p-2 d-flex">
                <input
                  className="form-control"
                  name={categoria.id}
                  type="text"
                  value={categoria.nombre}
                  aria-label="readonly input example"
                ></input>
               
               <button className="">Editar</button>
              </li>
            );
          })}
        </ul>
      </Modal.Body>
      <Modal.Footer>
        <div class="form-floating mt-2 mb-2 w-100 d-flex">
          <input
            type="text"
            className="form-control shadow-sm"
            id="floatingInput"
            placeholder="Crea una categora"
            value={inputCrearCategoria}
            onChange={handleChangeCrearCategoria}
          />
          <label for="floatingInput">Crea una categora</label>
          <button 
          onClick={crearCategoria}
          type="button" className="btn btn-dark shadow-sm">
            Crear
          </button>
        </div>
      </Modal.Footer>
    </Modal>
  );
};

export default ModalAdministrarCategoria;
