import React, {useState, useEffect} from "react";
import EstadisticasClientes from "../components/estadisticasClientes/EstadisticasClientes";
import EstadisticasComponent from "../components/estadisticasComponent/EstadisticasComponent";
import ListaDeClientes from "../components/listaDeClientes/ListaDeClientes";
import MostCommonComponent from "../components/mostCommonComponent/MostCommonComponent";
import { getClientes } from "../services/clientesService";

const ClientesScreen = () => {

  const [clientes, setClientes] = useState([]);
  const [cargando, setCargando] = useState(true)
  const [actualizadorClientes, setActualizadorClientes] = useState(false)

  useEffect(() => {
    // Obtenemos los clientes
    getClientes().then(data => {
      setClientes(data);
      setCargando(false);
    })
  }, [actualizadorClientes])

  return (
    <section className="row p-xxl-4 pb-xxl-2 pt-xl-4 ps-xl-4 pb-xl-2 p-2 pt-3 m-0">
      <div className="col-xl-9 col-12">
        <h2 className="title">Clientes</h2>

        <div className="col-12 pt-xxl-4 pt-2">
          <EstadisticasClientes numClientes={clientes.length} cargando={cargando}/>
        </div>

        <div className="col-12">
          <ListaDeClientes clientes={clientes} cargando={cargando} setActualizadorClientes={setActualizadorClientes}/>
        </div>
      </div>

      <div className="col-xl-3 col-6 p-0">
        {/* <MostCommonComponent title="Clientes frecuentes" /> */}
      </div>
    </section>
   );
};

export default ClientesScreen;
