import { db } from "../firebase/firebase";

export const postPedidos = async (objPedido) => {
  const peticion = await db.collection("pedidos").add(objPedido);
  if (!peticion.id) {
    console.log("ERROR AL CREAR PEDIDO");
    return;
  }
  return peticion;
};

export const getPedidos = async () => {
  
    const peticion = await db.collection("pedidos").get();
    if (!peticion.docs) {
      console.log("ERROR AL TRAER PEDIDOS DEL BACKEND");
      return;
    }
    const data = peticion.docs.map((doc) => ({
      id: doc.id,
      ...doc.data(),
    }));
    return data;
  
};

