export const getPedidoById = async (idPedido) => {
  try {
    const peticion = await db.collection("pedidos").doc(idPedido).get();
    if (!peticion.exists) return;
    return peticion.data();
  } catch (error) {
    console.log("ERROR AL TRAER EL PEDIDO POR ID", error);
  }
};

export const actualizarPedido = async (id, objPedido) => {
  try {
    const peticion = await db.collection("pedidos").doc(id).update(objPedido);
    return "pedido actualizado";
  } catch (error) {
    console.log("ERROR AL ACTUALIZAR EL PEDIDO: ", error);
  }
};
