import React, { useContext, useState } from "react";
import { Modal, Button } from "react-bootstrap";
import PosContext from "../../context/pos/posContext";
import { postCliente } from "../../services/clientesService";
import "./modalCrearCliente.css";
import Swal from "sweetalert2";

const ModalCrearCliente = () => {
  const { modalCrearCliente, setModalCrearCliente, setPuntoDeVentaState } =
    useContext(PosContext);

  const formVacio = {
    nombre: "",
    apellido: "",
    celular: "",
  };

  const [formCliente, setFormCliente] = useState(formVacio);

  const [mensajeError, setMensajeError] = useState("");

  const [activarBtnCrearCliente, setActivarBtnCrearCliente] = useState(true);

  const handleChange = (e) => {
    setMensajeError("");
    setFormCliente((prevState) => ({
      ...prevState,
      [e.target.name]: e.target.value,
    }));
  };

  const crearCliente = (e) => {
    e.preventDefault();
    // Validar formularios
    if (formCliente.nombre.trim().length < 3) {
      setMensajeError("Escribe un nombre vlido");
      return;
    } else if (formCliente.apellido.trim().length < 3) {
      setMensajeError("Escribe un apellido correcto");
      return;
    }

    // Bloqueamos el boton "crear cliente" para evitar crear duplicaciones
    setActivarBtnCrearCliente(false);

    // Creamos el cliente
    postCliente(formCliente).then((data) => {
      if (data.id) {
        Swal.fire({
          title: "Cliente Creado",
          icon: "success",
          timer: 900,
        });
        //Cerramos el modal
        setModalCrearCliente(false);
        // Una vez que el modal se cerr, habilitar el boton "crear" para la prxima creacin
        setActivarBtnCrearCliente(true);
        // Si el cliente se creo, entonces setear el cliente global
        setPuntoDeVentaState((prevState) => ({
          ...prevState,
          cliente_global: formCliente,
        }));

        // Reseteamos el formulario
        setFormCliente(formVacio);
      }
    });
  };

  return (
    <>
      <Modal
        show={modalCrearCliente}
        onHide={() => {
          setModalCrearCliente(false);
        }}
        backdrop="static"
        keyboard={false}
        centered
      >
        <Modal.Header>
          <Modal.Title>Crear Cliente Nuevo</Modal.Title>
        </Modal.Header>

        <Modal.Body>
          <form className="ps-4 pe-4" onSubmit={crearCliente}>
            <div className="row mb-3">
              <label htmlFor="name" className="form-label">
                Nombre
              </label>
              <input
                required
                type="text"
                id="name"
                name="nombre"
                className="form-control"
                value={formCliente.nombre}
                onChange={handleChange}
              />
            </div>

            <div className="row mb-3">
              <label htmlFor="name" className="form-label">
                Apellido
              </label>
              <input
                required
                type="text"
                id="name"
                className="form-control"
                name="apellido"
                value={formCliente.apellido}
                onChange={handleChange}
              />
            </div>
            <div className="mb-5 row ">
              <div className="col-7 p-0">
                <label htmlFor="name" className="form-label ps-2">
                  Celular (Opcional)
                </label>
                <input
                  type="text"
                  id="name"
                  className="form-control"
                  name="celular"
                  value={formCliente.celular}
                  onChange={handleChange}
                />
              </div>
            </div>
            {mensajeError.length > 0 && (
              <div class="alert alert-warning" role="alert">
                {mensajeError}
              </div>
            )}
            <hr />
            <div className="mb-3 mt-4 d-flex justify-content-end gap-3">
              <button
              class="btn btn-secondary"
                type="reset"
                onClick={() => {
                  setModalCrearCliente(false);
                  setFormCliente(formVacio);
                  setMensajeError("");
                }}
              >
                Cerrar
              </button>

              <button class="btn btn-dark" type={activarBtnCrearCliente ? "submit" : "button"}>
                Crear Cliente
              </button>
            </div>
          </form>
        </Modal.Body>
      </Modal>
    </>
  );
};

export default ModalCrearCliente;
