import React, { useState, useEffect } from "react";
import { storage } from "../../firebase/firebase";
import { getCategorias } from "../../services/categoriaService";
import { getPlatos, getPlatosByCategoria, postPlato } from "../../services/platosService";
import AdministrarProductosContext from "./administrarProductosContext";
import Swal from 'sweetalert2'
import moment from "moment";


const AdministrarProductosState = ({ children }) => {


  const [categoriaSeleccionada, setCategoriaSeleccionada] = useState(null); 
  const [actualizarPlatos, setActualizarPlatos] = useState(false)
  const [actualizarCategorias, setActualizarCategorias] = useState(false)

  // TRAER CATEGORIAS
  const [categoriasState, setCategoriasState] = useState({
    categorias: [],
    cargando: true,
  });

  useEffect(() => {
    getCategorias()
      .then((data) => {
        setCategoriasState({
          categorias: data,
          cargando: false,
        });
      })
      .catch((error) => {
        setCategoriasState((prevState) => ({
          ...prevState,
          cargando: false,
        }));
      });
  }, [actualizarCategorias]);
  //===========================

  // TRAER PLATOS SEGUN CATEGORIA SELECCIONADA
  const [platosState, setPlatosState] = useState({
    platos: [],
    cargando: true,
  });

  useEffect(() => {
    console.log("TRAYENDO PLATOS SEGUN CATEGORIA");
    if (categoriaSeleccionada) {
      console.log("CATEGORIA SELECCIONADA VLIDA");
      getPlatosByCategoria(categoriaSeleccionada.id).then((data) => {
        setPlatosState((prevState) => ({
          cargando: false,
          platos: data,
        }));
      });
    } else {
      setPlatosState((prevState) => ({
        ...prevState,
        cargando: false,
      }));
    }
  }, [categoriaSeleccionada, actualizarPlatos]);

  //========================
  
  // FUNCION PARA CREAR UN PLATO 
  const crearPlato = (objPlato) => {
    console.log("FUNCTION CREAR PLATO");

    // Primero subimos la imagen
    const uploadTask = storage.ref(`images/${objPlato.nombre}`).put(objPlato.imagen);
    uploadTask.on(
      "state_changed",
      (snapshot) => {
      },
      (error) => {
        console.log("ERROR AL SUBIR IMAGEN PARA CREAR PLATO");
        console.log(error);
      },
      () => {
        storage.ref("images")
        .child(objPlato.imagen.name)
        .getDownloadURL()
        .then(url => {
          /**
           * Una vez que firebase subio la imagen
           * le pasamos la url como imagen al plato
           */
          console.log("CREANDO PLATO LUEGO DE SUBIR LA IMAGEN ********");
          objPlato.imagen = url;
          console.log(objPlato);
          postPlato(objPlato).then(rpta => {
            if (rpta.id){
              console.log("PRODUCTO CREADO CON EXITO ==========");
              Swal.fire({
                title: "Plato creado exitosamente",
                icon: "success",
                timer: 300,
                showCancelButton: false
              })
              setActualizarPlatos(!actualizarPlatos)
            }
          })

        })
      }
    )

    // Creamos el plato completo, incluyendo ya  la url de la imagen subida

  }

  // FUNCION PARA EDITAR UN PLATO 
  const editarPlato = (objPlato) => {

  }

  // FUNCION PARA CREAR UNA CATEGORIA
  const crearCategoria = objCategoria => {

  }

  // FUNCION PARA EDITAR UNA CATEGORIA
  const editarCategoria = objCategoria => {

  }
  


  return (
    <AdministrarProductosContext.Provider
      value={{
        categorias: categoriasState.categorias,
        cargandoCategorias: categoriasState.cargando,
        platos: platosState.platos,
        setPlatosState,
        cargandoPlatos: platosState.cargando,
        categoriaSeleccionada, 
        setCategoriaSeleccionada, 
        crearPlato,
        crearCategoria,
        setActualizarPlatos,
        setActualizarCategorias
      }}
    >
      {children}
    </AdministrarProductosContext.Provider>
  );
};

export default AdministrarProductosState;
